// Widgets
//
// Orbit provides mapping between widget sizes and the susy grid.
// Note, widgets use a custom 100 col grid rather than the default 12 col grid to allow for correct widget sizes.
// You can view an example widget page at /widgets.html to see how they work with the grid.
// Feel free to change as needed (use caution though!).
// See sass/layout/_widgets.scss.
//
// Styleguide 19.5

.widget-row {
    @extend %cf;

    margin-top: $space-and-half;

    @include respond-min($mid-width) {
        margin-left: -$space;
        margin-right: -$space;
    }

    .home &:first-of-type {
        margin-top: 0;
    }
}

.widget-width {
    @include respond-min($mid-width) {
        float: left;
        padding: 0 $space;
        width: 100%;
    }
}

.widget-width--50 {
    &:last-child {
        .widget--navigation {
            @include respond-max($mid-width) {
                margin-bottom: $space;
            }
        }
    }
}

@include respond-min($mid-width) {
    @include widget-width;
}

.widget-stack {
    .widget:not(:last-child) {
        margin-bottom: $space;
    }
}

.widget {
    @extend %module;

    @include respond-max($mid-width) {
        margin-bottom: $space;
    }

    &__heading {
        &:first-child {
            margin-top: 0;
        }
    }

    &__subheading {
        &:first-child {
            margin-top: 0;
        }
    }

    &__text {
        margin-top: $space-half;
    }

    &__list {
        &__item {
            .widget__list {
                margin-top: $space-half;
            }
        }
    }

    &__footer {
        margin-top: $space-double;
        padding: 0 $space $border-width-xlarge;
        text-align: center;
    }
}

.widget--flush {

    &:first-child {
        padding-right: 0;
    }

    &:last-child {
        padding-left: 0;
    }
}
