$nav-link-size-px:    26px;
$nav-link-size:       $nav-link-size-px / $base-font-size * 1rem;

.list--navigation {
    color: $color-quaternary;
    list-style: disc;
    margin: 0 0 0 $space;
    position: relative;

    @include respond-min($mid-width) {
        margin: $space-double 0 0 $space-triple;
    }

    &::before {

        @include respond-min($mid-width) {
            background-color: $color-primary;
            content: '';
            display: block;
            height: $border-width-xlarge;
            left: -#{$space-triple};
            position: absolute;
            top: -#{$space-double};
            width: $space-and-two-thirds;
        }
    }

    .list__item {
        font-size: $nav-link-size;
        margin-top: $space-third;
        padding-left: $space-two-thirds;
    }

    .list__link {
        @include linkUnderlineStyling($color: $color-quaternary, $bg-color: $color-white);
        color: $color-quaternary;
        text-decoration: none;

        @include respond-min($mid-width) {
            background-position: 0 -.1em;
        }

        &:hover,
        &:focus,
        &:active {
            @include linkUnderlineHover();
            color: $color-link-hover;
        }
    }
}
