// ========================================================
//  Alerts
// ========================================================
// Alerts should be used to provide the user with action
// specific feedback, for example form submission errors.
// The alert block should always contain an alert__heading

// Alert
//
// Base alert class with modifiers
//
// Markup:
// <div class="alert {$modifiers}">
//     <h2 class="alert__heading">Alert Heading</h2>
//     <p class="alert__text">Some alert information</p>
// </div>
//
// .alert--success     - A success alert, useful for positive feedback such as a form submission
// .alert--warning     - A warning alert, useful for warning the user but not necessarily a error
// .alert--danger      - A danger alert, could be used, for example, to list form validation errors


// Local variables
$alertIconSize: $space-double + $space-quarter !default;

.alert {
    background-color: $color-white;
    padding: $space 0;
    position: relative;

    @include respond-max($small-width) {
        background-color: $color-off-white;
    }

    @include respond-min($small-width) {
        border-left-width: $space-quadruple;
        min-height: $alertIconSize * 2;
        padding: ($space - $space-sixth) $space $space;
    }

    &,
    * {
        color: $color-text-default;
    }

    .home-page &:first-child {
        margin-top: $space-and-half;
    }

    .container {
        padding-right: $space-double;
        position: relative;

        @include respond-min($small-width) {
            padding-left: $space-quadruple;
        }

        //  Icons
        // ======================
        &::before {
            background-size: contain;
            content: '';
            display: inline-block;
            height: $alertIconSize;
            margin: ($space-sixth * -1) $space-half $space-half ($space-sixth * -1);
            position: relative;
            vertical-align: middle;
            width: $alertIconSize;

            @include respond-min($small-width) {
                display: block;
                float: none;
                left: 0;
                margin: $space-two-thirds 0 0;
                position: absolute;
                top: 0;
            }

            @include respond-min($full-width) {
                left: $space-two-thirds;
            }
        }
    }

    > *:first-child {
        margin-top: 0;
    }
}

.alert__heading {
    @extend %boxed__heading;
    @extend %epsilon;

    font-family: $font-family;
    font-weight: $font-weight-normal;
    margin-bottom: $space-eighth;

    @include respond-max($small-width) {
        display: inline-block;
        margin-bottom: $space-third;
        vertical-align: middle;
        width: calc(100% - #{$alertIconSize + $space-half});
    }

    + .alert__text {
        width: 100%;
    }
}

.alert__subheading {
    @extend %boxed__heading;

    display: block;
    font-size: $zeta;
    margin: 0;
}

.alert__list {
    clear: both;
    margin-bottom: 0;
    margin-top: $space-half;
}

.alert__text {
    display: inline-block;
    font-size: $normalis;
    margin-bottom: 0;
    margin-top: $space-half;

    @include respond-max($small-width) {
        margin-top: 0;
        vertical-align: middle;
        width: calc(100% - #{$alertIconSize + $space-half});
    }
}

.alert__heading,
.alert__list,
.alert__text {
    @include respond-max($small-width) {
        &:last-child {
            padding-bottom: $space-half;
        }
    }
}

.alert__form {
    clear: both;

    @include respond-max($small-width) {
        background: $color-white;
        margin: $space-half ($space-half * -1) 0;
        padding: $space-half;
    }
}

.alert__link {
    color: $color-primary;
    display: inline-block;
    position: relative;
    text-decoration: underline;

    &:hover {
        text-decoration: none;
    }

    &:focus {
        box-shadow: 0 0 0 $border-width-large $color-quaternary;
    }
}

.alert__heading {
    + * {
        margin-top: 0;
    }
}

.alert__close {
    top: 0;

    @include respond-min($small-width) {
        right: -$space-third;
    }

    @include respond-min($full-width) {
        right: $space-and-third;
    }

    .site-header--internal + .site-main & {
        right: 0;
    }
}
