// =======
//  ICONS
// =======

// Local vars
$icon-size:         $space-and-third !default;
$icon-size--inline: .75em !default;
$icon-size--xsmall: $space-three-quarters !default;
$icon-size--small: $space !default;
$icon-size--large: $space-triple !default;

.icon {
    background-position: center center;
    background-size: contain;
    display: block;
    height: $icon-size;
    width: $icon-size;

    svg {
        max-height: 100%;
        max-width: 100%;
    }
}

.icon--xsmall {
    height: $icon-size--xsmall;
    width: $icon-size--xsmall;
}

.icon--small {
    height: $icon-size--small;
    width: $icon-size--small;
}

.icon--large {
    height: $icon-size--large;
    width: $icon-size--large;
}

.icon-fallback {
    @extend %visually-hidden;
}

.icon--inline {
    display: inline-block;
    height: $icon-size--inline;
    margin-top: remToEm(-$space-twelfth); // -$space-eighth but as EM
    vertical-align: middle;
    width: $icon-size--inline;

    &:last-child {
        margin-left: 1em;
    }

    &:first-child {
        margin-right: 1em;
    }

    &.icon--large {
        height: $icon-size;
        width: $icon-size;
    }
}

.js .icon--embeded {
    width: auto;
}

.icon--full {
    height: 100%;
    width: 100%;
}

// close vars
$close-icon-line-width: 3px;
$close-icon-width: 25px;
$close-background-height: 60px;

.close {
    background: none;
    border: 0;
    border-radius: 100%;
    cursor: pointer;
    display: block;
    height: $close-icon-width;
    position: absolute;
    right: $space-two-thirds;
    text-indent: -9999px;
    top: $space-two-thirds;
    transform: rotate(45deg);
    width: $close-icon-width;

    &:focus {
        box-shadow: 0 0 0 $border-width-large $color-quaternary;
        outline: 0;
    }

    &:hover,
    &:focus,
    &:active {
        &::before {
            height: calc(#{$close-icon-width} * 1.3);
        }

        &::after {
            width: calc(#{$close-icon-width} * 1.3);
        }
    }

    &::before {
        background-color: $color-primary;
        border-radius: $close-icon-line-width;
        content: '';
        height: $close-icon-width;
        left: 50%;
        position: absolute;
        top: 50%;
        transform: translate(-50%, -50%);
        transition: height $transition-speed-fast $transition-cubic-bezier;
        width: $close-icon-line-width;
    }

    &::after {
        background-color: $color-primary;
        border-radius: $close-icon-line-width;
        content: '';
        height: $close-icon-line-width;
        left: 50%;
        position: absolute;
        top: 50%;
        transform: translate(-50%, -50%);
        transition: width $transition-speed-fast $transition-cubic-bezier;
        width: $close-icon-width;
    }
}

.close__background {
    background-color: $color-quaternary;
    height: $close-background-height;
    position: absolute;
    transform: rotate(45deg) translate(-41px, 2px);
    width: $close-background-height;
    z-index: -1;
}

.close--white {
    &::before {
        background-color: $color-white;
    }

    &::after {
        background-color: $color-white;
    }
}

.title-with-crest {
    align-items: center;
    display: flex;

    &::before {
        background-position: center center;
        background-size: contain;
        content: '';
        display: inline-block;
        height: $space-and-half;
        margin-right: $space-half;
        width: $space-and-half;
        z-index: 1;
    }
}
