// Container
%container,
.container {
    @extend %cf;

    @include container;

    padding: 0 $space-two-thirds;
}

%container--flush,
.container--flush {
    padding-left: 0;
    padding-right: 0;
}

%container--flush-left,
.container--flush-left {
    padding-left: 0;
}

.container {
    .container {
        @extend %container--flush;
    }
}

.container--wide {
    max-width: $container-wide;
}

.outer-container {
    @extend %container;

    .modular & {
        max-width: 100%;
        padding-left: 0;
        padding-right: 0;
    }
}

.container--margin-top {
    margin-top: $space-double;
}
