.hero {
    background-position: center;
    background-size: cover;
    position: relative;

    @include respond-min($x-large-width) {
        margin: 0 auto;
        padding-bottom: $space-quadruple;
    }

    @include respond-min($xx-large-width) {
        max-width: $xx-large-width;
    }

    &::before {
        background-image: linear-gradient(-70deg, transparent 16%, $color-black 100%);
        bottom: 0;
        content: '';
        left: 0;
        opacity: .2;
        position: absolute;
        right: 0;
        top: 0;
    }

    .container {
        padding: $space-double $space-two-thirds;
        position: relative;
        z-index: 1;

        @include respond-min($small-width) {
            padding: $space-double;
        }
    }

    .hero__logo {
        height: pxToRem(70px);
        width: pxToRem(300px);
    }

    .hero__title {
        color: $color-white;
        line-height: 1.2;
        margin: 0 0 $space-double;

        @include respond-min($mid-width) {
            font-size: pxToRem(63px);
            margin: 0 $space 0 0;
            width: 60%;
        }
    }

    .hero__content {
        padding: $space-double 0;

        @include respond-min($mid-width) {
            align-items: center;
            display: flex;
            justify-content: flex-start;
            padding: $space-double $space-quadruple;
        }
    }

    .hero__select {
        background-color: transparent;
        background-position: right $space-two-thirds center;
        background-size: $space;
        border: $border-width-mid solid $color-white;
        border-radius: $button-border-radius;
        color: $color-white;
        font-size: $milli;
        padding: $space-half $space-double $space-half $space;
        text-transform: uppercase;
        transition: background-color $transition-speed-slow $transition-cubic-bezier;

        @include respond-min($mid-width) {
            max-width: 50%;
            white-space: pre-wrap;
            width: auto;
        }

        &:hover,
        &:focus,
        &:active {
            background-color: $color-quaternary;
            cursor: pointer;
        }
    }

    .hero__label {
        @include visuallyhidden();
    }
}
