// VARS / CONFIG
$download_meta-width:   18rem !default;
$download_break-point:  $small-width !default;
$download_border-width: 1px !default;

.download {
    border-bottom: $download_border-width solid $color-disabled;
    display: block;
    // @TODO: this should be controlled by the list
    padding: $space 0 0;

    @include respond-min($download_break-point) {
        padding: 0;
    }
}

.download__heading {
    display: block;
    font-size: $zeta;
    padding: 0 0 $space-half;

    @include respond-min($download_break-point) {
        display: inline-block;
        font-size: $normalis;
        padding-right: $space-half;
        padding-top: $space-half;
        vertical-align: bottom;
        width: calc(100% - #{$download_meta-width});
    }
}

.download__meta-list {
    @extend .list;
    @extend %cf;

    margin: 0;
    padding: 0;

    @include respond-min($download_break-point) {
        display: inline-block;
        text-align: right;
        vertical-align: bottom;
        width: $download_meta-width;
    }
}

.download__meta {
    background-color: $color-off-white;
    display: inline-block;
    margin: $space-half 0 $space-half $space-half;
    padding: 0 $space-sixth;
    vertical-align: bottom;

    @include respond-min($download_break-point) {
        font-size: $milli;
        margin-top: ($space-half / ($milli)) * 1rem;
    }
}

.download__meta--type {
    @include respond-min($download_break-point) {
        float: left;
        text-align: center;
        width: 3.5rem; // the width of "DOCX" with a bit of breathing space
    }
}

.download__meta--link {
    background-color: transparent;
    float: left;
    margin: 0;
    padding: 0;

    @include respond-min($download_break-point) {
        float: none;
        font-size: $normalis;
        margin-left: $space-half;
    }
}

.download__cta {
    background-color: $color-standout;
    border-bottom: $download_border-width solid mix($color-standout, $color-black, 80%);
    color: $color-standout-offset;
    display: block;
    margin: 0 0 $download_border-width * -1;
    padding: $space-half;

    &:hover,
    &:focus {
        background-color: lighten($color-standout, 5%);
        color: $color-standout-offset;
        text-decoration: none;
    }
}

