.top-tasks {

    @include respond-min($full-width) {
        margin-top: calc(#{$space-half} - #{$space-quadruple});
        padding: $space 0;
    }

    .top-tasks__list {
        display: flex;

        @include respond-max($full-width) {
            flex-wrap: wrap;
        }
    }

    .top-tasks__item {
        margin: 0;
        white-space: normal;
        width: 100%;
        z-index: 5;

        @include respond-min($small-width) {
            width: percentage(1 / 2);
        }

        @include respond-min($mid-width) {
            width: percentage(1 / 3);
        }

        @include respond-min($full-width) {
            margin: 0 -#{$space-two-thirds} 0 0;
            transform: translateY($space);
            width: calc(20% + #{$space-two-thirds});
        }

        &:nth-child(2n) {

            @include respond-min($full-width) {
                transform: translateY(-#{$space});
            }
        }

        &:nth-child(2) {
            z-index: 4;
        }

        &:nth-child(3) {
            z-index: 3;
        }

        &:nth-child(4) {
            z-index: 2;
        }

        &:nth-child(5) {
            z-index: 1;
        }
    }

    .top-tasks__item--yellow {
        background-color: $color-secondary;
    }

    .top-tasks__item--blue {
        background-color: $color-quaternary;

        .list__link {
            @include linkUnderline('.list__link-text', $color-white, $color-quaternary);
        }

        .list__link-text {
            color: $color-white;
        }
    }

    .top-tasks__item--turqoise {
        background-color: $color-standout;

        .list__link {
            @include linkUnderline('.list__link-text', $color-primary, $color-standout);
        }
    }

    .top-tasks__item--blue-dark {
        background-color: $color-primary;

        .list__link {
            @include linkUnderline('.list__link-text', $color-white, $color-primary);
        }

        .list__link-text {
            color: $color-white;
        }
    }

    .top-tasks__item--almond {
        background-color: $color-off-cream;

        .list__link {
            @include linkUnderline('.list__link-text', $color-primary, $color-off-cream);
        }
    }

    .list__link {
        @include linkUnderline('.list__link-text', $color-primary, $color-secondary);
        background-image: none;
        display: block;
        height: 100%;
        padding: $space-and-half;
        position: relative;
        text-shadow: none;
        width: 100%;

        @include respond-max($mid-width) {
            padding-right: $space-quadruple;
        }

        &:hover {
            .icon-task-chevron-right {
                margin: 0 0 $space calc(100% - #{$space-and-three-quarters});
            }
        }
    }

    .list__link-text {
        background-position: 0, -1em;
        display: inline;
        line-height: 1.2;
    }

    .icon-task-chevron-right {
        height: $space-double;
        margin: 0 0 $space calc(100% - #{$space-double});
        transition: margin $transition-speed $transition-cubic-bezier;
        width: $space-double;

        @include respond-max($mid-width) {
            margin: 0;
            position: absolute;
            right: $space;
            top: 50%;
            transform: translateY(-50%);
        }
    }
}
