.location {
    padding: $space 0;
    text-align: center;

    .title-with-crest {
        justify-content: center;
    }

    .location__text {
        font-size: $normalis;
    }

    .location__form {
        margin-top: $space-and-half;

        @include respond-min($mid-width) {
            display: flex;
            justify-content: center;
        }
    }

    .location__label {
        @include visuallyhidden();
    }

    .location__field {
        border: $border-width-mid solid darken($color-grey-light, 10);

        @include respond-min($mid-width) {
            margin: 0 $space;
        }

        &:focus,
        &:active {
            background: $color-white;
        }
    }

    .location__current-location {
        align-items: center;
        background-color: transparent;
        border: 0;
        display: flex;
        margin: 0 $space 0 0;
        min-width: $space-triple * 2;
        padding: 0;

        @include respond-max($mid-width) {
            margin-top: $space;
            width: 50%;
        }

        &:focus {
            box-shadow: 0 0 0 $border-width-large $color-link-focus;
        }

        &::before {
            display: none;
        }
    }

    .location__cl-text {
        font-size: $micro;
        line-height: 1.1;
        margin-left: $space-third;
        text-align: left;
        text-transform: none;
    }

    .location__input-container {
        align-items: center;
        display: flex;
        flex-wrap: wrap;

        @include respond-min($mid-width) {
            flex-wrap: nowrap;
        }

        @include respond-max($mid-width) {
            justify-content: space-between;
        }
    }

    .location__button {
        margin: 0;

        @include respond-max($mid-width) {
            margin-top: $space;
        }
    }

    .icon-ui-location {
        flex: none;
        height: $space-and-fifth;
        width: $space-and-fifth;
    }

    .icon-ui-pins {
        flex: none;

        @include respond-max($mid-width) {
            margin: 0 auto $space;
        }
    }
}
