$logo-width: 11em;

.site-header--internal {
    background-color: $color-primary;

    @include respond-max($mid-width) {
        padding-top: $space-two-thirds;
    }

    .container--flush-left {
        padding-left: $space-two-thirds;

        @include respond-max($mid-width) {
            padding-left: 0;
        }
    }

    .identity {
        display: block;
        height: 100%;

        @include respond-max($mid-width) {
            height: $space-double;
            text-align: center;
        }
    }

    .identity__link {
        display: inline-block;
        height: 100%;

        &:focus {
            box-shadow: inset 0 0 0 $border-width-large $color-link-focus;
        }
    }

    .identity__logo {
        height: 100%;
        width: $logo-width;
    }

    .nav--primary-navigation {
        display: flex;

        @include respond-max($mid-width) {
            display: block;
        }

        .list {
            @include respond-max($mid-width) {
                justify-content: space-between;
            }
        }

        .list__item {

            &:nth-child(2) {
                margin-left: 0;
            }

            &:last-child {
                background-color: $color-white;
                transition: background-color $transition-speed $transition-cubic-bezier;

                &:hover {
                    background-color: $color-standout;
                }
            }
        }

        .list__link {

            .icon {
                margin-right: 0;
            }
        }
    }

    .list__link-text {
        @include visuallyhidden();
    }
}
