$social-list-size: 100px;
$share-arrow-height: 16px;
$share-arrow-width: 10px;

.share {
    display: inline-block;
    position: relative;

    &.is-open {
        .share__social-list {
            opacity: 1;
            transform: translate(-50%, -#{$social-list-size / 1.2});
            visibility: visible;

            @include respond-max($full-width) {
                transform: translate(-#{$social-list-size * 1.25}, -10%);
            }

            &::before {
                height: 60%;
            }

            &::after {
                width: 60%;
            }
        }
    }

    // scss-lint:disable ImportantRule
    &.open-delay {
        .share__social-list[aria-hidden='true'] {
            display: flex !important;
        }
    }
    // scss-lint:enable ImportantRule
}

.share__social-list {
    background-color: $color-secondary;
    border-radius: $social-list-size / 2;
    display: flex;
    flex-wrap: wrap;
    height: $social-list-size;
    left: 50%;
    list-style: none;
    margin: 0;
    opacity: 0;
    padding: 0;
    position: absolute;
    top: -100%;
    transform: translate(-50%, -#{$social-list-size / 1.4});
    transition: opacity $transition-speed $transition-cubic-bezier,
                transform $transition-speed $transition-cubic-bezier,
                visibility $transition-speed $transition-cubic-bezier;
    visibility: hidden;
    width: $social-list-size;
    z-index: 1;

    @include respond-max($full-width) {
        transform: translate(-#{$social-list-size * 1.15}, -10%);
    }

    &::before {
        background-color: $color-white;
        content: '';
        height: 10%;
        left: 50%;
        position: absolute;
        top: 50%;
        transform: translate(-50%, -50%);
        transition: height $transition-speed $transition-cubic-bezier;
        width: $border-width-small;
    }

    &::after {
        background-color: $color-white;
        content: '';
        height: $border-width-small;
        left: 50%;
        position: absolute;
        top: 50%;
        transform: translate(-50%, -50%);
        transition: width $transition-speed $transition-cubic-bezier;
        width: 10%;
    }
}

.share__social-item {
    position: relative;
    width: 50%;

    &:first-child {
        .share__social-link {
            border-top-left-radius: 100%;
        }
    }

    &:nth-child(2) {
        .share__social-link {
            border-top-right-radius: 100%;
        }
    }

    &:nth-child(3) {
        .share__social-link {
            border-bottom-left-radius: 100%;
        }
    }

    &:last-child {
        .share__social-link {
            border-bottom-right-radius: 100%;
        }
    }

    .icon {
        position: absolute;

        &::before {
            background-color: $color-primary;
            bottom: 0;
            content: '';
            height: $border-width-small;
            left: 50%;
            opacity: 0;
            position: absolute;
            transform: translateX(-50%);
            transition: opacity $transition-speed $transition-cubic-bezier,
                        width $transition-speed $transition-cubic-bezier;
            width: $border-width-mid;
        }
    }

    &:first-child {

        .icon {
            bottom: $space-sixth;
            right: $space-sixth;
        }
    }

    &:nth-child(2) {
        .icon {
            bottom: $space-sixth;
            left: $space-sixth;
        }
    }

    &:nth-child(3) {
        .icon {
            right: $space-sixth;
            top: $space-sixth;
        }
    }

    &:last-child {
        .icon {
            left: $space-sixth;
            top: $space-sixth;
        }

        &::after {
            border-color: $color-secondary transparent transparent;
            border-style: solid;
            border-width: $share-arrow-width #{$share-arrow-height / 2} 0;
            bottom: -#{$share-arrow-height / 2 + 1};
            content: '';
            height: 0;
            left: -#{$share-arrow-height / 2};
            position: absolute;
            width: 0;

            @include respond-max($full-width) {
                border-color: transparent transparent transparent $color-secondary;
                border-width: #{$share-arrow-height / 2} 0 #{$share-arrow-height / 2} $share-arrow-width;
                bottom: initial;
                left: initial;
                right: -#{$share-arrow-height / 2 + 1};
                top: -#{$share-arrow-height / 2};
            }
        }
    }
}

.share__social-link {
    align-items: center;
    display: flex;
    height: 100%;
    width: 100%;

    &:hover,
    &:focus,
    &:active {
        .icon {
            &::before {
                opacity: 1;
                width: $border-width-xxlarge * 2;
            }
        }
    }

    &:focus {
        box-shadow: 0 0 0 $border-width-large $color-quaternary;
        position: relative;
        z-index: 1;
    }
}

.share__button {
    background-color: transparent;
    border: 0;
    border-radius: 0;
    height: $space;
    margin: 0 0 $space-quarter;
    overflow: hidden;
    padding: 0;
    text-indent: -9999px;
    width: $space;

    &::before {
        display: none;
    }

    &:focus {
        box-shadow: 0 0 0 $border-width-large $color-quaternary;
    }
}
