// Images
//
// Base image styling along with various options for image overlays, captions, thumbnails and avatars (WIP)
// See sass/objects/_images.scss.
//
// Styleguide 10

img {
    display: block; // remove weird invisible bottom margin
}

.image {
    &--feature {
        float: right;
        margin: $space 0 $space $space;
        max-width: 250px;
        min-width: 100px;
        width: 35%;
    }

    &--avatar {
        max-width: 60px;

        img {
            border: 2px solid $color-black;
            height: auto;
        }
    }

    &--gallery {
        display: block;
    }

    &--caption {
        @extend %module;

        float: right;
        margin: $space 0 $space $space;
        max-width: 250px;
        min-width: 100px;
        width: 35%;

        img {
            width: 100%;
        }

        &__text {
            color: $color-text-muted;
            margin-top: 0;
        }
    }

    &--icon {
    }

    &--thumbnail {
        height: auto;
        max-width: 150px;
        width: 20%;
    }
}
