.cta {
    background-color: $color-quaternary;
    padding: $space;

    @include respond-min($large-width) {
        padding: $space-double $space-double $space-double $space-quadruple;
    }

    .cta__title {
        color: $color-quaternary-offset;
        margin-top: $space-half;
    }

    .cta__text {
        color: $color-quaternary-offset;
        font-size: $normalis;
        margin: 0;
        position: relative;

        &::before {

            @include respond-min($large-width) {
                background-color: $color-white;
                content: '';
                display: block;
                height: $border-width-xlarge;
                left: 0;
                position: absolute;
                top: $space-third;
                transform: translateX(-150%);
                width: $space-and-two-thirds;
            }
        }
    }

    .cta__button {
        font-size: $micro;
        margin-top: $space;
    }
}
