.bookmarks {
    background-color: $color-quaternary;
    position: absolute;
    transform: translateY(-100%);
    transition: transform $transition-speed $transition-cubic-bezier;
    width: 100%;
    z-index: 1;

    .container {
        display: flex;
        position: relative;

        @include respond-max($mid-width) {
            flex-wrap: wrap;
        }
    }

    &.vertical-is-open {
        transform: translateY(0);
    }

    // scss-lint:disable ImportantRule
    &.vertical-delay[aria-hidden='true'] {
        display: flex !important;
    }
    // scss-lint:enable ImportantRule

    .no-js & {
        position: static;
        transform: translateY(0);
    }
}

.bookmarks__list {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    margin: 0;
    padding: $space $space $space-half;

    @include respond-min($mid-width) {
        width: 80%;
    }
}

.bookmarks__list-item {
    border: $border-width-small solid rgba($color-white, .2);
    margin: 0 $space-half $space-half 0;
    position: relative;
    transition: border $transition-speed $transition-cubic-bezier;
    width: 100%;

    @include respond-min($small-width) {
        width: calc(100% / 2 - #{$space-half});
    }

    @include respond-min($large-width) {
        width: calc(100% / 3 - #{$space-half});
    }

    &::before {
        border: $border-width-small solid $color-white;
        content: '';
        display: block;
        height: 100%;
        left: 0;
        opacity: 0;
        position: absolute;
        top: 0;
        transition: opacity $transition-speed $transition-cubic-bezier;
        width: 100%;
    }

    &:hover {
        border-color: $color-white;

        &::before {
            opacity: 1;
        }
    }

    .icon-pin {
        position: absolute;
        right: $space-two-thirds;
        top: 50%;
        transform: translateY(-50%);
    }
}

.bookmarks__list-item--active {
    background-color: $color-primary;
    border-color: $color-primary;
}

.bookmarks__list-item--placeholder {

    &:hover {
        border-color: rgba($color-white, .2);

        &::before {
            display: none;
        }
    }
}

.bookmarks__list-link {
    align-items: center;
    color: $color-white;
    display: flex;
    font-size: $normalis;
    font-weight: $font-weight-normal;
    height: 100%;
    padding: $space-half $space-and-two-thirds $space-half $space-two-thirds;
    position: relative;
    z-index: 1;

    &:hover,
    &:focus,
    &:visited {
        color: $color-white;
        text-decoration: none;
    }
}

.bookmarks__content {
    color: $color-white;
    padding: $space;

    @include respond-max($mid-width) {
        order: -1;
    }
}

.bookmarks__title {
    align-items: baseline;
    color: $color-white;
    display: flex;
    font-size: $gamma;
    margin: 0;
    position: relative;

    &-line {
        background-color: $color-white;
        content: '';
        display: block;
        height: $border-width-xlarge;
        margin-left: $space;
        width: $space-and-two-thirds;
    }
}

.bookmarks__text {
    font-size: $normalis;
    margin-bottom: 0;

    @include respond-min($small-width) {
        width: percentage(3 / 4);
    }
}

.bookmark__close {
    right: $space-half;
    top: 50%;
    transform: translateY(-50%) rotate(45deg);

    &:hover {
        &::before {
            height: $zeta-px;
        }

        &::after {
            width: $zeta-px;
        }
    }

    &::before {
        height: $zeta-px;
    }

    &::after {
        width: $zeta-px;
    }

    .bookmarks__list-link:hover & {
        &::before {
            height: $zeta-px * 1.2;
        }

        &::after {
            width: $zeta-px * 1.2;
        }
    }
}

.bookmarks__close {
    @include respond-min($full-width) {
        right: $space-and-third;
    }

    &:focus {
        box-shadow: 0 0 0 $border-width-large $color-link-focus;
    }
}
