// .list
//
// Base list class, nothing fancy - no list style, includes border-box. Feel free to change.
// Appearence can be changed using the below modifiers.
// See sass/objects/_lists.scss.
//
// Markup:
// <ul class="list {$modifiers}">
//  <li class="list__item">List item 1</li>
//  <li class="list__item">List item 2</li>
//  <li class="list__item">List item 3</li>
//  <li class="list__item">List item 4</li>
// </ul>
//
// .list--rich     - Adds a richer styling to the list
// .list--inline   - Makes the list inline
// .list--striped  - Gives the list a striped appearence
//
.list,
%list {
    font-size: $zeta;
    list-style: none;
    margin-left: 0;

    &__item {
    }

    &__link {
    }
}
