// ========================================================
//  Content
// ========================================================
//  This file is intentionally sparse and are intended as
//  placeholder for your content styles that arent covered
//  by the objects in /sass/objects/. Orbit contains the
//  following content styles as default, feel free to
//  change as needed.

.site-main {
    overflow: hidden;
    position: relative;

    .home & {
        margin-bottom: $space-double;
    }
}

.main-content {
    &,
    &:focus,
    &:active {
        box-shadow: none;
        outline: 0;
    }
}

.site-content {
    @extend %cf;

    .one-column & {
        margin: 0 auto;
    }

    // .two-column & {
    //     @include respond-min($mid-width) {
    //         float: left;
    //         padding-right: $space-half;
    //         width: percentage(9 / 12);
    //     }
    //
    //     @include respond-min($large-width) {
    //         padding-right: $space;
    //     }
    //
    //     @include respond-min-and-max($large-width, $x-large-width) {
    //         width: percentage(8.5 / 12);
    //     }
    // }
}

.page-content {
    .two-column & {
        @include respond-min($mid-width) {
            float: left;
            padding-right: $space-half;
            width: percentage(9 / 12);
        }

        @include respond-min($large-width) {
            padding-right: $space;
        }

        @include respond-min-and-max($large-width, $x-large-width) {
            width: percentage(8.5 / 12);
        }
    }
}

.page-header {
    background-color: $color-subtle;
    padding: $space-double 0;

    .landing-page & {

        @include respond-min($x-large-width) {
            padding: $space-double 0 $space-quadruple;
        }
    }
}

.page-article {
    &:focus {
        box-shadow: none;
    }
}

.page-heading {
    margin-top: 0;
}

.page__heading-container {
    align-items: flex-end;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    .page-heading {
        padding-right: $space-half;
        width: calc(100% - #{$space});
    }
}
