.carousel {
    background-color: $color-grey-light;
    margin-top: $space;
    padding: $space;
    position: relative;

    @include respond-min($large-width) {
        padding: $space-double $space-double $space-double #{$space * 5};
    }

    .icon-social-twitter-primary {
        bottom: $space;
        position: absolute;
        right: $space-and-half;
        z-index: 1;
    }

    .flickity-page-dots {
        bottom: initial;
        left: $space-triple;
        top: $space-double;
        width: auto;

        @include respond-max($large-width) {
            display: none;
        }

        .dot {
            background: transparent;
            border: $border-width-mid solid $color-primary;
            display: block;
            margin: $space-third 0;
            opacity: 1;
            transition: background-color $transition-speed $transition-cubic-bezier;

            &.is-selected,
            &:hover,
            &:active {
                background-color: $color-primary;
            }
        }
    }
}

.slide {
    padding: 0 $space-sixth;
    width: 100%;
}

.slide__heading {
    color: $color-primary;
    font-size: $delta;
    margin: 0 0 $space-half;
}

.slide__text {
    font-size: $normalis;
    margin: 0 0 $space-half;
}

.slide__date {
    font-size: $normalis;
}
