.button--standout,
%button--standout {
    &,
    &:visited {
        border-color: darken($color-standout, 10%);
        color: $color-primary;
    }

    &:hover,
    &:active,
    &:focus {
        color: $color-primary;

        &::before {
            border-color: darken($color-standout, 10%);
        }
    }
}
