.button--success,
%button--success {
    &,
    &:visited {
        border-color: darken($color-success, 10%);
        color: $color-primary;
    }

    &:hover,
    &:active,
    &:focus {
        color: $color-primary;

        &::before {
            border-color: darken($color-success, 10%);
        }
    }
}
