.button--danger,
%button--danger {
    &,
    &:visited {
        border-color: darken($color-danger, 10%);
        color: $color-primary;
    }

    &:hover,
    &:active,
    &:focus {
        color: $color-primary;

        &::before {
            border-color: darken($color-danger, 10%);
        }
    }
}
