// Boxed
//
// Creates wells/boxes for content. See sass/objects/_boxed.scss.
//
// Markup:
// <div class="boxed {$modifiers}">
//     <h3 class="boxed__heading">Some awesome content</h3>
//     <p class="boxed__text">Lorem ipsum dolor sit amet, consectetur adipiscing elit. Suspendisse nec velit pharetra, cursus nisi eu, viverra leo. Praesent tempus, massa ornare elementum iaculis, augue libero ultrices sapien, eget lobortis eros eros non ex.</p>
// </div>
//
// .boxed--primary     - Primary background colour
// .boxed--secondary   - Secondary background colour
// .boxed--standout    - Standout background colour
// .boxed--subtle      - Subtle background colour
//
// Styleguide 4
.boxed,
%boxed {
    @extend %cf;
    @extend %module;

    background-color: $color-subtle;
    border-color: $color-subtle;
    margin: $space 0 $space-double;
    padding: $space;

    &__heading {
        @extend %epsilon;

        margin-top: 0;
    }

    &__subheading {
    }

    &__list {
    }

    &__text {
    }

    &__form {
        background: none;
        border: 0;
        box-shadow: none;
        margin: 0;
        padding: 0;
    }
}
