// Forms
//
// Extends the `%form-settings` sass placeholder. Fields, label, selects and text areas span 100% width by default.
// For accessibility reasons, forms should have fieldsets/legends were possible.
// See sass/objects/_forms.scss.
//
// Styleguide 8

// .form
//
// Extends the `%form-settings` sass placeholder. Has additional form specific varibles
//
// Markup:
// <form class="form {$modifiers}">
//  <h2 class="form__heading">Default form</h2>
//  <p class="form__text">This is some descriptive text about the form. It will provide context to the user for the following steps.</p>
//  <div class="form__control">
//      <label class="form__label" for="forename">First name</label>
//      <input class="form__field" type="text" id="forename" name="forename" placeholder="John">
//  </div>
//  <div class="form__control">
//      <label class="form__label" for="surname">Last name</label>
//      <input class="form__field" type="text" id="surname" name="surname" placeholder="Doe">
//  </div>
//  <div class="form__control form__control--append">
//      <label class="form__label" for="surname">Find address</label>
//      <div class="form__control--append__group">
//          <input class="form__field" type="text" id="postcode" name="postcode" placeholder="AB12 3CD">
//          <button class="button button--small">Search</button>
//      </div>
//      <span class="form__help">This text helps the user understand what to put here</span>
//  </div>
//  <div class="form__control">
//      <label class="form__label" for="age">Age</label>
//      <select class="form__select" id="age">
//          <option>1-10</option>
//          <option>11-21</option>
//          <option>21+</option>
//      </select>
//  </div>
//  <div class="form__control">
//      <span class="form__label">Favourite colour</span>
//      <div class="form__control__group">
//          <label class="form__label form__label--checkbox">
//              <input class="form__checkbox" type="checkbox" name="gender" value="red">
//              Red
//          </label>
//          <label class="form__label form__label--checkbox">
//              <input class="form__checkbox" type="checkbox" name="gender" value="yellow">
//              Yellow
//          </label>
//      </div>
//  </div>
//  <div class="form__control">
//      <span class="form__label">Gender</span>
//      <div class="form__control__group">
//          <label class="form__label form__label--radio">
//              <input type="radio" class="form__radio" name="gender" value="Male">
//              Male
//          </label>
//          <label class="form__label form__label--radio">
//              <input type="radio" class="form__radio" name="gender" value="Female">
//              Female
//          </label>
//          <label class="form__label form__label--radio">
//              <input type="radio" class="form__radio" name="gender" value="Other">
//              Other
//          </label>
//      </div>
//  </div>
//  <button class="button button--primary" type="submit">Submit</button>
// </form>
//
// .form--inline    - Puts the form elements next to each other
// .form--standout  - Applies the standout background colour
//
// Styleguide 8.1

/*
 Variables
\*----------------------------------------------------------------------------*/
// Form specific varibles

// Styled forms
// scss-lint:disable NestingDepth
.form,
%form {
    margin-bottom: $space;

    fieldset {
        border: 0;
        padding-top: 0;

        &:first-of-type {
            border: 0;
            padding: 0;
        }
    }

    legend {
        @extend %epsilon;

        color: $color-primary;
        display: block;
        margin: 0 0 1px; // form legends in Chrome upset the VR without this
        white-space: normal;
        width: 100%;
    }

    input[type='search'] {
        -webkit-appearance: none;
        box-sizing: border-box;
    }

    .button {
        margin-top: $space-half;

        &--small {
            margin-top: $space-two-thirds;
        }
    }

    &__label,
    &__field,
    &__select,
    &__textarea {
        display: block;
    }

    &__field,
    &__select,
    &__textarea,
    &__checkbox,
    &__radio {

        &:focus {
            box-shadow: 0 0 0 $border-width-large $color-link-focus;
            outline: 0;
        }
    }

    &__radio {
        appearance: none;
        border: $border-width-mid solid $color-primary;
        border-radius: $space-three-quarters;
        height: $space-three-quarters;
        width: $space-three-quarters;

        &:checked {
            background-color: $color-primary;
        }
    }

    &__checkbox {
        -webkit-appearance: none;
        border: $border-width-mid solid $color-primary;
        height: $space-three-quarters;
        width: $space-three-quarters;

        &:checked {
            background-color: $color-primary;
            position: relative;

            &::before {
                background-color: $color-white;
                content: '';
                display: block;
                height: $space-fifth;
                left: 55%;
                position: absolute;
                top: 70%;
                transform: rotate(130deg);
                transform-origin: 0 0 0;
                width: $border-width-mid;
            }

            &::after {
                background-color: $color-white;
                content: '';
                display: block;
                height: $border-width-mid;
                left: 35%;
                position: absolute;
                top: 70%;
                transform: rotate(-50deg);
                transform-origin: 0 0 0;
                width: $space-third;
            }
        }
    }

    &__label--radio,
    &__label--checkbox {
        align-items: center;
        display: flex;
    }

    &__heading {
        @extend .boxed__heading;
    }

    &__text {
    }

    &__help {
        @extend %micro;

        background-color: $color-subtle;
        color: $color-text-muted;
        display: block;
        margin: $space-half 0 0;
        padding: $space-half;
    }

    &__link {
    }

// .form__control
//
// Provides a wrapper for form elements that provides padding and an additional style hook for positioning and states.
// Labels and corresponding inputs should always be wrapped in a `.form__control`.
//
// Markup:
// <form class="form">
//  <fieldset>
//      <legend>Default form</legend>
//      <div class="form__control {$modifiers}">
//          <label class="form__label" for="first-name">First name</label>
//          <input class="form__field" type="text" id="first-name">
//      </div>
//      <div class="form__control">
//          <input type="button" name="button" class="button button--primary" value="Submit">
//      </div>
//  </fieldset>
// </form>
//
// .form__control--danger    - Applies danger styling to the form_controls children (useful for highlighting validation errors on a field level)
// .form__control--success   - Applies success styling to the form_controls children (useful for highlighting resolved validation errors on a field level)
//
// Styleguide 8.1
    &__control {
        margin: $space 0;

        &--half {
        }

        &--danger {
            .form__label {
                color: $color-danger;
            }

            .form__field,
            .form__select,
            .form__textarea {
                background-color: lighten($color-danger, 40%);
                border-color: $color-danger;
                color: $color-danger;
            }
        }

        &--success {
            .form__label {
                color: $color-success;
            }

            .form__field,
            .form__select,
            .form__textarea {
                background-color: lighten($color-success, 40%);
                border-color: $color-success;
                color: $color-success;
            }
        }

        &__group {
            margin-top: $space-quarter;
        }

// .form__control--append
//
// Modifier for `.form` where form only contains one visable field and button. The button will be appended to the field
// useful for search forms. UNTESTED - This needs reviewing and tweaking (WIP), needs mark up example.
//
// Styleguide 8.4
        &--append {
            &__group {
                height: calc(#{$space-half} + (#{$base-font-size} * #{$line-height-ratio}));
                margin-top: $space-half;
                position: relative;

                .form__field,
                .button {
                    margin-top: 0;
                }

                .form__field {
                    height: 100%;
                    left: 0;
                    padding-right: calc(#{$space-half} + #{span(4)});
                    position: absolute;
                    top: 0;
                    width: 100%;
                }

                .button {
                    height: 100%;
                    position: absolute;
                    right: 0;
                    top: 0;
                    width: span(4);
                }
            }

            &--full {
                width: 100%;

                .form__label {
                    @extend %visually-hidden;
                }

                .form__field,
                .button {
                    margin-top: 0;
                }
            }
        }
    }

// .form__label
//
// Basic form label styling
//
// Styleguide 8.2
    &__label {
        align-self: center;
        margin-top: $space-half;
        padding: 0;
        width: 100%;

        &--checkbox {
            margin-top: $space-quarter;
        }

        &--radio {
            @extend .form__label--checkbox;
        }
    }


// .form__field
//
// Basic form field styling
//
// Markup:
// <form class="form">
//  <h2>Form field example</h2>
//  <div class="form__control">
//      <label class="form__label" for="first-name">First name</label>
//      <input class="form__field form__field--disabled" type="text" id="first-name">
//  </div>
//  <div class="form__control">
//      <input type="button" name="button" class="button button--primary" value="Submit">
//  </div>
// </form>
//
// .form__field--disabled    - Applies disabled styling to the to the field
//
// Styleguide 8.3
    &__field {
        @extend %normalis;

        background-color: $color-white;
        border: 0;
        border-bottom: $border-width-mid solid $color-primary;
        color: $color-text-default;
        font-family: $font-family;
        margin-top: $space-half;
        max-width: 100%;
        padding: $space-half;
        width: 100%;

        &:focus {
            background-color: $color-subtle;
        }

        &--disabled {
            border-color: $color-disabled;
        }

        &--upload {
        }
    }

    &__select {
        @extend .form__field;

        appearance: none;
        background-position: right center;
        border-radius: 0;

        &::-ms-expand {
            display: none;
        }
    }

    &__textarea {
        @extend .form__field;
    }

// .form__checkbox
//
// Basic form checkbox styling
//
// Styleguide 8.4
    &__checkbox {
        display: inline-block;
        margin-right: $space-quarter;
    }

// .form__radio
//
// Basic form radio styling - extends `.form__checkbox`
//
// Styleguide 8.4
    &__radio {
        display: inline-block;
        margin-right: $space-quarter;
    }
}

// Default form styling
form:not([class]) {
    @extend %form;

    input:not([type='button']):not([type='checkbox']):not([type='radio']):not([type='submit']),
    textarea {
        @extend .form__field;
    }

    select {
        @extend .form__select;
    }

    input[type='button'],
    input[type='submit'] {
        @extend %button;
    }

    input[type='checkbox'],
    input[type='radio'] {
        @extend .form__checkbox;
    }
}

.form__append-group {
    display: flex;

    .button {
        border-bottom-left-radius: 0;
        border-color: $color-primary;
        color: $color-primary;
        margin-bottom: 0;

        &:hover,
        &:focus,
        &:active {

            &::before {
                border-color: $color-primary;
            }
        }

        &::before {
            border-bottom-left-radius: 0;
            z-index: 1;
        }
    }
}
// scss-lint:enable NestingDepth
