// ========================================================
//  Brand palette
// ===============

//  Primary palette
// =================
$primary-palette: (
    'blue-dark':                #2d3547,
    'yellow':                   #fdcc00,
    'blue':                     #4d71b8,
    'turquoise':                #6ee0ce,
    'almond':                   #f0e8d4,
    'custard':                  #feefb3,
    'blue-pale':                #e4e9f4,
    'grey-light':               #f8f8f8,
    'purple-grey':              #5f647c
);

//  Neutral palette
// =================
$color-black:                   #000;
$color-grey-dark:               #383835;
$color-grey:                    #616365;
$color-grey-light:              #f8f8f8;
$color-off-white:               #fafaf8;
$color-white:                   #fff;
$color-off-cream:               map-get($primary-palette, 'almond');

// ========================================================


// ========================================================
//  JDU brand palette
// ====================
$color-jadu-blue:               #95bed9;
$color-jadu-blue-dark:          #2080c0;
$color-spacecraft-yellow:       #ffe807;

// ========================================================


// ========================================================
//  Non-brand palette
// ===================

//  UI palette
// ============
//  These can updated to use colours from the `Primary palette`
$ui-palette: (
    'blue':                    #226fb4,
    'blue-dark':               #095294,
    'green':                   #2eb156,
    'orange':                  #e98300,
    'purple':                  #6d2077,
    'red':                     #c0232e,
    'yellow':                  #ffe13b
);

// ========================================================




// ========================================================
//  Site Colours
// ==============

//  Primary
// =========
$color-primary:                 map-get($primary-palette, 'blue-dark');
$color-primary-offset:          $color-white;

//  Text
// ======
$color-text-default:            $color-primary;
$color-text-heading:            $color-primary;
$color-text-muted:              $color-grey;
$color-text-disabled:           darken($color-grey-light, 5%);

//  Secondary
// ===========
$color-secondary:               map-get($primary-palette, 'yellow');
$color-secondary-offset:        $color-text-default;

//  Tertiary
// ===========
$color-tertiary:                map-get($primary-palette, 'purple-grey');
$color-tertiary-offset:         $color-white;

//  Quaternary
// ===========
$color-quaternary:                map-get($primary-palette, 'blue');
$color-quaternary-offset:         $color-white;

//  Quinary
// ===========
$color-quinary:                map-get($primary-palette, 'custard');
$color-quinary-offset:         $color-primary;

//  Senary
// ===========
$color-senary:                map-get($primary-palette, 'blue-pale');
$color-senary-offset:         $color-primary;

//  Response
// ==========
$color-success:                 map-get($ui-palette, 'green');
$color-warning:                 map-get($ui-palette, 'orange');
$color-danger:                  map-get($ui-palette, 'red');
$color-disabled:                $color-grey-light;

//  Stand-out
// ===========
$color-standout:                map-get($primary-palette, 'turquoise');
$color-standout-offset:         map-get($primary-palette, 'blue-dark');

//  Link
// ======
$color-link:                    map-get($ui-palette, 'blue');
$color-link-visited:            map-get($ui-palette, 'blue');
$color-link-hover:              map-get($ui-palette, 'blue-dark');
$color-link-focus:              map-get($ui-palette, 'yellow');
$color-link-outline:            rgba($color-link, .15);

//  Misc
// ======
$color-site-background:         $color-grey-light;
$color-subtle:                  map-get($primary-palette, 'blue-pale');
$color-border:                  map-get($primary-palette, 'purple-grey');
$color-border-input:            map-get($primary-palette, 'blue-dark');
$color-border-light:            map-get($primary-palette, 'blue');

// ========================================================
