.form__control--materialize {
    margin-top: $space-third;
    padding-top: $space-quarter;
    position: relative;

    .form__label {
        left: 0;
        position: absolute;
        top: $space-half;
        transform-origin: top left;
        transition: color $transition-speed-fast, transform $transition-speed-fast;
        z-index: 1;
    }

    .form__field {
        padding-left: 0;
        transition: padding-left $transition-speed-fast ease-out;
    }

    .form__append-group {
        position: static;

        .form__label {
            width: auto;
        }
    }

    .form__field:focus {
        padding-left: $space-third;
    }

    .form__field:focus,
    .form__field[placeholder],
    .form__field--not-empty {
        outline-offset: 0;

        + .form__label {
            color: $color-text-muted;
            transform: scale(.9) translateY(-$space-and-third);
        }
    }

    .form__field[placeholder] {
        + .form__label {
            color: $color-text-default;
        }
    }

    .no-js & {
        .form__label {
            transform: scale(.9) translateY(-$space-and-third);
        }

        .form__append-group {
            .form__label {
                top: $space-half;
            }

            .form__field:focus,
            .form__field[placeholder],
            .form__field--not-empty {

                + .form__label {
                    transform: scale(.9) translateY(-$space-and-third);
                }
            }
        }
    }
}
