.more-about {
    padding: $space 0;
    text-align: center;

    .more-about__title {
        justify-content: center;
        margin: 0;
    }

    .list__item {

        &:not(:last-child) {
            margin-right: $space-half;

            &::after {
                content: '|';
            }

            .list__link {
                margin-right: $space-half;
            }
        }
    }
}
