.form--inline,
%form--inline {
    .form__control {
        padding-left: 4px;
        width: 100%;

        &--group {
            margin-top: 0;
        }

        &--append__group {
            width: calc(#{span(9)} - #{$space-half});
        }

        > *:not(.form__help) {
            display: inline-block;
            margin-left: -4px;
            margin-top: 0;
            max-width: calc(#{span(9)} - #{$space-half});
            vertical-align: top;
        }

        .form__label {
            display: inline-block;
            margin-right: $space;
            margin-top: $space-fifth;
            width: auto;
        }

        > .form__label {
            width: span(3);
        }

        .form__help {
            margin-left: calc(#{span(3)} + #{$space} - 5px);
        }
    }
}
