// Styleguide 6

$calendar_max-width: 24rem !default;

.calendar {
    display: block;
    margin: $space 0;
    max-width: $calendar_max-width;
    min-width: 100px;

    th,
    td {
        margin: 0;
        padding: 0;
        text-align: center;
    }

    .button {
        display: inline-block;
        margin-bottom: $space-third;
        margin-top: -$space-third;
    }
}

.calendar__heading {
    @extend %normalis;

    text-align: center;
}

.calendar__table {
    width: 100%;
}

.calendar__days {
    background-color: $color-primary;
    color: $color-white;

    th {
        font-weight: bold;
        padding: 3.5% $space-sixth 4%;
    }
}

.calendar__dates {
    tr {
        border-bottom: 1px solid $color-disabled;
    }
}

.calendar__date {
    color: $color-disabled;
    display: block;
    height: 100%;
    padding: 26% $space-quarter;
}

.calendar__date--today {
    background-color: mix($color-link, $color-white, 5%);
}

.calendar__date--link {
    color: $color-link;
    font-weight: $font-weight-bold;

    &:hover,
    &:focus {
        background-color: mix($color-link, $color-white, 10%);
    }

    &:visited {
        background-color: mix($color-link, $color-white, 14%);
    }
}


