.side-nav {
    background-color: $color-standout;
    display: flex;
    flex-direction: column;
    height: 100vh;
    justify-content: flex-start;
    overflow-y: auto;
    padding: $space;
    position: absolute;
    right: 0;
    top: 0;
    transform: translateX(100%);
    transition: transform $transition-speed $transition-cubic-bezier;
    width: $side-nav-width;

    .js & {
        position: fixed;
    }

    .open-menu & {
        transform: translateX(0);
    }

    // scss-lint:disable ImportantRule
    &.menu-delay[aria-hidden='true'] {
        display: flex !important;
    }
    // scss-lint:enable ImportantRule

    svg {
        height: 40px;
    }

    .close {
        right: 0;
        top: 0;

        &:focus {
            box-shadow: 0 0 0 $border-width-large $color-link-focus;
        }
    }
}

.side-nav__main {
    flex-grow: 1;
    transform: translateX(40%);
    transition: transform $transition-speed $transition-cubic-bezier;

    .no-js &,
    .open-menu & {
        transform: translateX(0);
    }
}

.side-nav__footer {
    transform: translateX(40%);
    transition: transform $transition-speed $transition-cubic-bezier;

    .no-js &,
    .open-menu & {
        transform: translateX(0);
    }
}

.side-nav__list {
    list-style: none;
    margin: $space-two-thirds 0 0;
}

.side-nav__list-item {
    margin-bottom: $space-quarter;
}

.side-nav__list-link {
    @include linkUnderlineStyling($color: $color-primary, $bg-color: $color-standout);

    color: $color-primary;
    cursor: pointer;
    font-weight: $font-weight-bold;
    position: relative;
    text-decoration: none;

    &:hover,
    &:focus,
    &:visited {
        @include linkUnderlineHover();

        color: $color-primary;
    }

    .side-nav__main & {
        font-size: $epsilon;
        text-transform: uppercase;
    }

    .side-nav__footer & {
        font-weight: $font-weight-normal;
    }
}

button.side-nav__list-link {
    background: transparent;
    border: 0;
    padding: 0 $space-and-third 0 0;

    &:focus {
        box-shadow: 0 0 0 $border-width-large $color-link-focus;
        color: $color-primary;
        outline: 0;
    }
}

.side-nav__sublist {
    background-color: $color-primary;
    display: none;
    list-style: none;
    margin: $space-two-thirds -#{$space};
    overflow: hidden;

    .is-open + &,
    .no-js & {
        display: block;
    }
}

.side-nav__sublist-item {
    padding: .2em 2em;
    transition: transform $transition-speed $transition-cubic-bezier;

    // scss-lint:disable ImportantRule
    .is-open & {
        transform: translateX(0) !important;
    }
    // scss-lint:enable ImportantRule

    &:first-child {
        padding-top: 2em;
    }

    &:last-child {
        padding-bottom: 2em;
    }
}

.side-nav__sublist-link {
    @include linkUnderlineStyling($color: $color-white, $bg-color: $color-primary);
    color: $color-white;
    font-weight: $font-weight-normal;
    text-decoration: none;

    &:hover,
    &:focus,
    &:visited {
        @include linkUnderlineHover();
        color: $color-white;
    }
}

.side-nav__copyright {
    color: $color-primary;
    padding-top: .2em;
}

.side-nav__crest {
    height: $space-and-two-thirds;
    width: $space-and-two-thirds;
}

.icon-arrow {
    display: inline-block;
    height: $space-half;
    position: absolute;
    right: 0;
    top: 40%;
    transform: translateY(-50%) rotate(225deg);
    transition: transform $transition-speed-fast $transition-cubic-bezier;
    width: $space-half;

    .is-open & {
        transform: rotate(45deg);
    }

    &::before {
        background-color: $color-primary;
        content: '';
        height: $border-width-mid;
        left: 0;
        position: absolute;
        top: 0;
        width: 100%;
    }

    &::after {
        background-color: $color-primary;
        content: '';
        height: 100%;
        left: 0;
        position: absolute;
        top: 0;
        width: $border-width-mid;
    }
}
