// Styleguide 18.4
$footer-logo-width: 12em;

.site-footer {
    @extend %module;

    background-color: $color-primary;
    padding: $space 0 $space-double;

    @include respond-max($mid-width) {
        text-align: center;
    }

    .container {

        @include respond-min($mid-width) {
            display: flex;
        }
    }

    .site-footer__column {
        &:last-child {
            flex-grow: 1;
        }
    }

    .list {
        margin: 0 0 0 -#{$space-two-thirds};
    }

    .site-footer__social .list__item {
        display: inline-block;

        &:first-child {
            .list__link {
                margin-left: 0;
            }
        }
    }

    .site-footer__social .list__link {
        display: block;
        margin: $space-half;
        position: relative;

        @include respond-max($small-width) {
            margin: $space-third;
        }

        &::before {
            border: $border-width-mid solid $color-standout;
            border-radius: 50%;
            content: '';
            display: block;
            height: 10%;
            left: 50%;
            opacity: 0;
            position: absolute;
            top: 50%;
            transform: translate(-50%, -50%);
            transition: $transition-speed $transition-cubic-bezier;
            width: 10%;
        }

        &:hover {
            &::before {
                height: 120%;
                opacity: 1;
                width: 120%;
            }
        }
    }

    .site-footer__links .list__item {
        display: inline-block;
        padding: $space-half;
    }

    .site-footer__links .list__link {
        @include linkUnderlineStyling($color: $color-white, $bg-color: $color-primary);
        color: $color-white;
        font-size: $normalis;

        &:hover,
        &:focus,
        &:visited {
            @include linkUnderlineHover();
            text-decoration: none;
        }
    }

    .site-footer__info {

        @include respond-min($mid-width) {
            align-items: center;
            display: flex;
            flex-wrap: wrap;
        }
    }

    .site-footer__rights {
        color: $color-white;
        font-size: $normalis;
        margin: 0 $space 0 0;
    }

    .suppliers {
        color: $color-white;
        font-size: $milli;
        margin: 0;
        width: 100%;
    }

    .suppliers__supplier {
        margin-right: $space-half;
    }

    .suppliers__link {
        @include linkUnderlineStyling($color: $color-white, $bg-color: $color-primary);
        color: $color-white;

        &:hover,
        &:focus,
        &:visited {
            @include linkUnderlineHover();
            text-decoration: none;
        }
    }

    .identity__link {
        display: block;
    }

    .identity__logo {
        display: block;
        height: $space-double;
        margin-top: $space-three-quarters;
        width: $footer-logo-width;

        @include respond-max($mid-width) {
            margin: $space-double auto 0;
        }
    }
}
