$pages-in-item-px:                    26px;
$pages-in-item:                       $pages-in-item-px / $base-font-size * 1rem;

.nav--pages-in,
%nav--pages-in {

    .boxed {
        margin-top: 0;
    }

    .nav__heading {
        align-items: center;
        display: flex;
        margin-top: 0;

        svg {
            margin-right: $space-two-thirds;
        }
    }

    .list {
        list-style: decimal;
        margin: $space-three-quarters 0 0;
    }

    .list__item {
        color: $color-primary;
        font-size: $pages-in-item;
        margin: 0 0 0 $space;
        padding-left: 0;
    }

    .list__item--active-child {
        font-weight: $font-weight-bold;

        .list__link {
            font-weight: $font-weight-bold;

            &:hover,
            &:focus {
                background-size: 0;
            }
        }
    }

    .list__link {
        @include linkUnderlineStyling($color: $color-primary, $bg-color: $color-standout);
        color: $color-primary;
        font-weight: $font-weight-normal;
        text-decoration: none;

        &:hover,
        &:focus {
            @include linkUnderlineHover();
        }
    }
}
