%list--inline,
.list--inline {
    margin: 0;

    .list__item {
        display: inline-block;
        margin: $space-half $space 0 0;
        white-space: nowrap;

        @include respond-max($x-small-width) {
            width: 100%;
        }

        &:last-child {
            margin-right: 0;
        }
    }

    .list__link {
        @include linkUnderline(null, $color-primary, $color-white);
        color: $color-primary;
        font-size: $normalis;
        font-weight: $font-weight-normal;
        text-decoration: none;
    }
}
