// Links
//
// Base link styling
// See sass/objects/_link.scss.
//
// :hover       - Hover state.
// :active      - Active state.
// :focus       - Focus state.
//
// Markup:
// <a class="{$modifiers}">Link</a>
//
// Styleguide 11

a {
    color: $color-link;
    font-weight: $font-weight-bold;

    &,
    &:visited {
        text-decoration: none;
    }

    &:visited {
        color: $color-link-visited;
    }

    &:hover,
    &:focus,
    &:active {
        color: $color-link-hover;
        text-decoration: underline;
    }

    &:focus {
        box-shadow: 0 0 0 $border-width-large $color-link-focus;
        outline: 0;
    }
}
