.teasers {
    display: flex;

    @include respond-max($large-width) {
        flex-wrap: wrap;
    }

    @include respond-min($large-width) {
        padding: 0 $space-double;
    }

    &__title {
        margin: $space-and-two-thirds 0 $space;

        @include respond-min($small-width) {
            margin: $space-and-two-thirds $space-double $space;
        }
    }

    &--full {
        padding: 0;

        @include respond-min($large-width) {
            margin-top: $space;
        }

        .teaser {

            @include respond-min($large-width) {
                margin-right: -#{$space};
                width: calc(33.3333% + #{$space-two-thirds});
                z-index: 1;
            }

            &:nth-child(2) {

                @include respond-min($large-width) {
                    margin: 0 -#{$space} 0 0;
                    transform: translateY(-#{$space});
                }
            }
        }

        .teaser__image::after {
            display: none;
        }

        .teaser__content {
            margin-bottom: $space;
            padding: 0 $space;

            @include respond-min($full-width) {
                margin: $space $space-triple $space $space-quadruple;
                padding: 0;
            }

            &::before {
                @include respond-max($full-width) {
                    opacity: 0;
                }
            }
        }

        .teaser--yellow {
            background-color: $color-secondary;

            &:focus {
                box-shadow: 0 0 0 $border-width-large $color-quaternary;
            }

            .teaser__title {
                @include linkUnderlineStyling($color: $color-secondary-offset, $bg-color: $color-secondary);
            }
        }

        .teaser--navy {
            background-color: $color-primary;

            .teaser__date {
                color: $color-primary-offset;
            }

            .teaser__title {
                @include linkUnderlineStyling($color: $color-primary-offset, $bg-color: $color-primary);
                color: $color-primary-offset;
            }

            .teaser__content::before {
                background-color: $color-primary-offset;
            }
        }

        .teaser--turquoise {
            background-color: $color-standout;

            .teaser__title {
                @include linkUnderlineStyling($color: $color-standout-offset, $bg-color: $color-standout);
            }
        }
    }
}

.teaser {
    width: 100%;

    @include respond-min($small-width) {
        width: 50%;
    }

    @include respond-min($large-width) {
        width: percentage(1 / 3);
    }

    &:hover,
    &:focus,
    &:active {
        text-decoration: none;

        .teaser__image::after {
            height: $border-width-xxlarge;
        }

        .teaser__img {
            transform: scale(1.1);
        }

        .teaser__title {
            @include linkUnderlineHover();
        }
    }

    &:nth-child(2) {

        @include respond-min($large-width) {
            margin: 0 $space-triple;
        }

        @include respond-max($small-width) {
            display: none;
        }
    }

    &:last-child {
        @include respond-max($large-width) {
            display: none;
        }
    }

    &--almond {
        .teaser__image::after {
            background-color: $color-off-cream;
        }
    }

    &--navy {
        .teaser__image::after {
            background-color: $color-primary;
        }
    }

    &--turquoise {
        .teaser__image::after {
            background-color: $color-standout;
        }
    }

    .teaser__image {
        min-height: $border-width-xxxlarge;
        overflow: hidden;
        position: relative;

        &::before {
            background-color: rgba($color-black, .2);
            bottom: 0;
            content: '';
            left: 0;
            position: absolute;
            right: 0;
            top: 0;
            z-index: 1;
        }

        &::after {
            bottom: 0;
            content: '';
            height: $border-width-xxxlarge;
            left: 0;
            position: absolute;
            transition: height $transition-speed-slow $transition-cubic-bezier;
            width: 100%;
            z-index: 2;
        }
    }

    .teaser__img {
        transition: transform $transition-speed-slow $transition-cubic-bezier;
        width: 100%;
    }

    .teaser__content {
        margin-top: $space;
        position: relative;

        @include respond-max($large-width) {
            padding: 0 $space;
        }

        &::before {
            @include respond-min($large-width) {
                background-color: $color-primary;
                content: '';
                display: block;
                height: $border-width-xlarge;
                left: -#{$giga};
                margin-left: $space;
                position: absolute;
                top: $space-half;
                width: $space-and-two-thirds;
            }
        }
    }

    .teaser__date {
        color: $color-primary;
        display: block;
        font-weight: $font-weight-normal;
        margin-bottom: $space-two-thirds;
    }

    .teaser__title {
        @include linkUnderlineStyling($color: $color-primary, $bg-color: $color-white);
        background-position: 0 -#{$space-eighth};
        color: $color-primary;
        display: inline;
        font-size: $delta;
        line-height: 1.4;
    }
}
