// Paginator navigation
//
// Pagination style navigation
//
// Markup:
// <nav class="pagination">
//     <ol class="pagination__list">
//         <li classpaginationitem pagination__item--previous"><span class="pagination__link is-disabled">Previous</span></li>
//         <li class="pagination__item pagination__item--next"><a class="pagination__link" href="#">Next</a></li>
//     </ol>
// </nav>
//
// <nav class="pagination pagination--complex">
//     <ol class="pagination__list">
//         <li class="pagination__item pagination__item--previous"><span class="pagination__link is-disabled">Previous</span></li>
//         <li class="pagination__item"><span class="pagination__link is-active">1</span></li>
//         <li class="pagination__item"><a class="pagination__link" href="#">2</a></li>
//         <li class="pagination__item"><a class="pagination__link" href="#">3</a></li>
//         <li class="pagination__item pagination__item--next"><a class="pagination__link" href="#">Next</a></li>
//     </ol>
// </nav>
//
// Styleguide 14.3

$arrow-width: 2px;

.pagination {
    background-color: $color-grey-light;
    display: block;
    margin-top: $space;
    padding: $space;
}

.pagination__list {
    @extend %cf;
    @extend %list;
    align-items: center;
    display: flex;
    list-style: none;
    margin: 0;
}

.pagination__item {
    display: inline-block;
    width: calc(50% - #{$space-and-two-thirds / 2});
}

.pagination__logo {

    svg {
        display: block;
        width: 100%;
    }
}

.pagination__link {
    color: $color-primary;
    display: block;
    padding: 0 $space-and-third;
    position: relative;

    &:visited,
    &:hover,
    &:focus,
    &:active,
    .is-active {
        color: $color-primary;
        text-decoration: none;
    }
}

.pagination__label {
    display: block;
    font-size: $epsilon;
    font-weight: $font-weight-bold;
    text-transform: uppercase;
}

.pagination__meta {
    @include linkUnderlineStyling($color: $color-primary, $bg-color: $color-grey-light);
    font-weight: $font-weight-normal;

    .pagination__link:hover &,
    .pagination__link:focus & {
        @include linkUnderlineHover();
    }
}

.pagination__item--previous {
    text-align: right;

    .pagination__link--arrow {

        @include respond-max($mid-width) {
            padding-left: 0;
        }

        &:hover,
        &:focus,
        &:active {

            .pagination__label {

                &::before {

                    @include respond-min($mid-width) {
                        opacity: 1;
                        transform: rotate(45deg) translateY(-50%);
                    }
                }

                &::after {

                    @include respond-min($mid-width) {
                        opacity: 1;
                        transform: rotate(-45deg) translateY(50%);
                    }
                }
            }
        }

        .pagination__label {

            &::before {
                background-color: $color-primary;
                content: '';
                height: 50%;
                left: 0;
                opacity: 0;
                position: absolute;
                top: 30%;
                transform: translateY(-50%);
                transition: opacity $transition-speed $transition-cubic-bezier,
                            transform $transition-speed $transition-cubic-bezier;
                width: $arrow-width;
            }

            &::after {
                background-color: $color-primary;
                content: '';
                height: 50%;
                left: 0;
                opacity: 0;
                position: absolute;
                top: 30%;
                transform: translateY(50%);
                transition: opacity $transition-speed $transition-cubic-bezier,
                            transform $transition-speed $transition-cubic-bezier;
                width: $arrow-width;
            }
        }
    }
}

.pagination__item--next {

    .pagination__link--arrow {

        @include respond-max($mid-width) {
            padding-right: 0;
        }

        &:hover,
        &:focus {

            .pagination__label {

                &::before {

                    @include respond-min($mid-width) {
                        opacity: 1;
                        transform: rotate(-45deg) translateY(-50%);
                    }
                }

                &::after {

                    @include respond-min($mid-width) {
                        opacity: 1;
                        transform: rotate(45deg) translateY(50%);
                    }
                }
            }
        }

        .pagination__label {

            &::before {
                background-color: $color-primary;
                content: '';
                height: 50%;
                opacity: 0;
                position: absolute;
                right: 0;
                top: 30%;
                transform: translateY(-50%);
                transition: opacity $transition-speed $transition-cubic-bezier,
                            transform $transition-speed $transition-cubic-bezier;
                width: $arrow-width;
            }

            &::after {
                background-color: $color-primary;
                content: '';
                height: 50%;
                opacity: 0;
                position: absolute;
                right: 0;
                top: 30%;
                transform: translateY(50%);
                transition: opacity $transition-speed $transition-cubic-bezier,
                            transform $transition-speed $transition-cubic-bezier;
                width: $arrow-width;
            }
        }
    }
}

.pagination__item--previous,
.pagination__item--next {
    .is-disabled {
        color: darken($color-disabled, 20%);

        .pagination__meta {
            background-size: 0;
            color: darken($color-disabled, 20%);
        }
    }
}

.pagination__crest {
    height: $space-and-two-thirds;
    width: $space-and-two-thirds;
}

.pagination--complex {
    background-color: $color-white;
    padding: 0;

    .pagination__list {
        justify-content: center;
    }

    .pagination__item {
        width: auto;
    }

    .pagination__label {
        @include linkUnderline($color: $color-link, $bg-color: $color-white);
        color: $color-quaternary;
        font-size: $zeta;
        text-transform: none;
    }

    .pagination__link {
        padding: 0;
    }

    .pagination__item--number {

        @include respond-max($small-width) {
            display: none;
        }

        .pagination__link {
            @include linkUnderline($color: $color-link, $bg-color: $color-white);
            color: $color-quaternary;
            margin: 0 $space-half;
            padding: 0 $space-fifth;

            &.is-active {
                background-size: 0;
                color: $color-primary;
                font-size: .8em;
                font-weight: $font-weight-bold;
                position: relative;
                text-shadow: none;
                z-index: 0;

                &::before {
                    background: $color-standout;
                    border-radius: 13px;
                    content: '';
                    display: block;
                    height: 26px;
                    left: calc(50% - 13px);
                    position: absolute;
                    text-align: center;
                    top: -$space-eighth;
                    width: 26px;
                    z-index: -1;
                }
            }
        }

    }

    .pagination__item--previous {
        padding-right: $space-and-third;
    }

    .pagination__item--next {
        padding-left: $space-and-third;
    }
}

.pagination--forms {

    .pagination__label {
        @include linkUnderlineStyling($color: $color-primary, $bg-color: $color-grey-light);
        display: inline;
    }

    .pagination__link {

        &:hover,
        &:focus {

            .pagination__label {
                @include linkUnderlineHover();
            }
        }
    }

    .is-disabled {
        .pagination__label {
            color: darken($color-disabled, 20%);
        }

        &.pagination__link {

            &:hover,
            &:focus {

                .pagination__label {
                    background-size: 0;
                }
            }
        }
    }
}
