.list--rich {
    .list__item {
        @extend %cf;

        border-bottom: 1px solid $color-subtle;
        padding: $space-half;

        &:first-child {
            border-top: 1px solid $color-subtle;
            padding-top: $space-half;
        }
    }

    h2,
    h3,
    h4 {
        font-size: $epsilon;
        margin-bottom: $space-half;
    }

    .image--thumbnail,
    .image--avatar {
        float: right;
        margin: 0 0 $space-third $space-third;
        width: 25%;
    }
}
