// 2 Account form widgets:
// '.widget--my-account-forms' isn't in the repo
// '.widget--myaccount-form-submissions' = 58

.widget--my-account-forms,
.widget--myaccount-form-submissions {
    > div {
        margin-bottom: $space;

        &:last-of-type {
            margin-bottom: 0;
        }
    }

    .item-list {
        margin-bottom: 0;

        > li {
            border-bottom: 1px solid $color-primary;
            margin-bottom: $space-quarter;
            padding-bottom: $space-quarter;

            li {
                border-bottom: 0;
                margin-bottom: 0;
                padding-bottom: 0;
            }
        }
    }
}

.widget--my-account-forms {
    h3 {
        @extend %boxed__subheading !optional;
    }
}

// Widget 58
.widget--myaccount-form-submissions {
    h3 {
        @extend %boxed__heading !optional;
    }

    h4 {
        @extend %normalis;

        margin-top: $space;
    }

    p {
        margin-bottom: 0;
    }

    .item-list {
        margin-bottom: $space;
    }

    .button {
        margin-top: $space-half;
        padding: $space-third;
    }
}
