.translate {
    background-color: $color-off-cream;
    position: absolute;
    transform: translateY(-100%);
    transition: transform $transition-speed $transition-cubic-bezier;
    width: 100%;
    z-index: 1;

    .container {
        position: relative;
    }

    .translate__close {
        top: $space-two-thirds;

        @include respond-min($full-width) {
            right: $space-and-third;
        }
    }

    &.vertical-is-open {
        transform: translateY(0);
    }

    // scss-lint:disable ImportantRule
    &.vertical-delay[aria-hidden='true'] {
        display: block !important;
    }
    // scss-lint:enable ImportantRule

    .no-js & {
        position: static;
        transform: translateY(0);
    }

    .translate__list {
        display: flex;
        flex-wrap: wrap;
        list-style: none;
        margin: 0;
        padding-right: $space-double;

        @include respond-max($small-width) {
            .list__item {
                width: 50%;
            }
        }
    }

    .list__item {
        padding: $space-two-thirds;

        &:last-child {
            .translate__list-link {
                font-weight: $font-weight-normal;
            }
        }
    }

    .list__link {
        @include linkUnderlineStyling($color: $color-primary, $bg-color: $color-off-cream);
        color: $color-primary;
        font-size: $milli;

        &:hover,
        &:focus,
        &:visited {
            @include linkUnderlineHover();
            color: $color-primary;
            text-decoration: none;
        }

        &:focus {
            box-shadow: 0 0 0 $border-width-large $color-quaternary;
        }
    }
}
