// Sidebar
//
// This file is intentially sparse and are intended as placeholder for your sidebar styles.
// Orbit includes the folowing sidebar styles as default, feel free to change as you need.
// See sass/layout/_sidebars.scss.
//
// ```
// .sidebar--primary {
//     .lt-ie9 & {
//         @include span(4 at 9 of 12 split);
//     }
//     @include respond-min($small-width) {
//         @include span(4 at 9 of 12);
//     }
// }
// ```
//
// Styleguide 18.5
.sidebar {
    @extend %module;

    @include respond-min($mid-width) {
        float: left;
        width: percentage(3 / 12);
    }

    @include respond-min-and-max($large-width, $x-large-width) {
        width: percentage(3.5 / 12);
    }

    &--2up {
        margin-top: $space-double;

        @include respond-min($mid-width) {
            display: flex;
        }

        .sidebar__section {
            display: block;
            padding-left: 0;
            padding-right: 0;
            width: 100%;

            @include respond-min($mid-width) {
                width: percentage(1 / 2);
            }

            &:first-child {
                margin-right: $space-double;
            }
        }
    }

    .one-column & {
        float: none;
        width: 100%;
    }

    .sidebar__section {
        margin-bottom: $space;
    }

    .sidebar__heading {
        @extend h6;
        margin-top: 0;
    }

    .list__link {
        align-items: center;
        background-image: none;
        color: $color-quaternary;
        display: flex;
        font-size: $zeta;
        font-weight: $font-weight-bold;
        text-shadow: none;

        &:hover,
        &:focus,
        &:active {
            .list__link-text {
                color: $color-link-hover;
                text-decoration: underline;
            }
        }

        .icon {
            margin-left: $space-half;
        }
    }
}

// Modifiers
@import 'sidebars/primary';
@import 'sidebars/secondary';
