// ========================================================
//  Type
// ========================================================
// Apply various font sizes by class. Pragmatic & practical font sizing
// as per: http://csswizardry.com/2012/02/pragmatic-practical-font-sizing-in-css/

// Type sizes
// ========================================================
//  Oversized Type Range
// ======================
%giga,
.giga {
    font-size: $giga;
    margin: $space-half 0;
}

%mega,
.mega {
    font-size: $mega;
    margin: $space-half 0;
}

%kilo,
.kilo {
    font-size: $kilo;
    margin: $space 0 $space-two-thirds;
}

//  Standard Type Range
// =====================
%alpha,
.alpha {
    font-size: $alpha;
    margin: $space-half 0 0;
}

%beta,
.beta {
    font-size: $beta;
    margin: $space-two-thirds 0 $space-quarter;
}

%gamma,
.gamma {
    font-size: $gamma;
    margin: $space 0 0;
}

%delta,
.delta {
    font-size: $delta;
    margin: $space-three-quarters 0 0;
}

%epsilon,
.epsilon {
    font-size: $epsilon;
    margin: $space-third 0 0;
}

%zeta,
.zeta {
    font-size: $zeta;
    margin: $space-third 0 0;
}

//  Base Type Size
// ================
%normalis,
.normalis {
    font-size: $normalis;
    margin: $space-half 0 0;
}

//  Base Type Reset
// =================
%type-reset {
    font-family: $font-family;
    font-size: $normalis;
    font-weight: $font-weight-normal;
    line-height: $line-height-ratio;
}

//  Small Type Range
// ==================
%milli,
.milli {
    font-size: $milli;
    margin: $space-half 0 0;
}

%micro,
.micro {
    font-size: $micro;
    margin: $space-two-thirds 0 0;
}

// ========================================================


//  Headings
// ========================================================
//  Heading styling. All headings are also available as
//  classes. Eg: .h1, .h2 etc
//  Ref: http://csswizardry.com/2012/02/pragmatic-practical-font-sizing-in-css/
h1,
h2,
h3,
h4,
h5,
h6 {
    color: $color-text-default;
    font-family: $font-family-heading;
    font-weight: $font-weight-heading;
}

h1 {
    @extend .alpha;
}

h2 {
    @extend .beta;
    color: $color-tertiary;
    margin: $space-double 0 $space;
}

h3 {
    @extend .gamma;
    color: $color-tertiary;
}

h4 {
    @extend .delta;
    color: $color-tertiary;
}

h5 {
    @extend .epsilon;
    font-family: $font-family;
    text-transform: uppercase;
}

h6 {
    @extend .zeta;
    font-family: $font-family;
}

.h1 {
    @extend h1;

    display: block;
}

.h2 {
    @extend h2;

    display: block;
}

.h3 {
    @extend h3;

    display: block;
}

.h4 {
    @extend h4;

    display: block;
}

.h5 {
    @extend h5;

    display: block;
}

.h6 {
    @extend h6;

    display: block;
}

// ========================================================

p {
    font-size: $zeta;
}
