.services {
    background-color: rgba($color-off-cream, .99);
    overflow-y: auto;
    transition: opacity $transition-speed $transition-cubic-bezier;
    width: 100%;

    .js & {
        height: 100vh;
        left: 0;
        opacity: 0;
        position: fixed;
        top: 0;
        z-index: 1000;
    }

    &.is-open {
        .js & {
            opacity: 1;
        }
    }

    // scss-lint:disable ImportantRule
    &.is-delayed[aria-hidden="true"] {
        display: block !important;
    }
    // scss-lint:enable ImportantRule

    .services__content {
        margin: $space-quadruple $space;

        @include respond-min($large-width) {
            margin: $space-quadruple;
        }
    }

    .services__title {
        margin: 0 0 $space;
        text-align: center;

        @include respond-min($mid-width) {
            font-size: $giga;
        }
    }

    .services__list {
        align-items: center;
        display: flex;
        flex-wrap: wrap;

        .list__item {
            display: inline;
            margin-bottom: $space;
            width: 100%;

            @include respond-min($small-width) {
                width: 50%;
            }

            @include respond-min($mid-width) {
                width: percentage(1 / 3);
            }

            @include respond-min($full-width) {
                width: 25%;
            }
        }

        .list__link {
            align-items: center;
            color: $color-primary;
            display: flex;
            font-size: pxToRem(22px);
            font-weight: $font-weight-normal;
            padding: $space;
            position: relative;
            text-decoration: none;
            z-index: 1;

            &:hover,
            &:focus,
            &:active {
                .list__link-text {
                    @include linkUnderlineHover();
                }

                .services__icon {
                    &::before {
                        opacity: 1;
                    }
                }
            }

            &:focus {
                box-shadow: 0 0 0 $border-width-large $color-quaternary;
            }
        }

        .list__link-text {
            @include linkUnderlineStyling($color: $color-primary, $bg-color: $color-off-cream);
            display: inline;
            line-height: 1.2;
            margin-left: $space-two-thirds;
        }
    }

    .services__icon {
        flex: none;
        position: relative;

        &::before {
            background-color: $color-standout;
            border-radius: 100%;
            content: '';
            height: 140%;
            left: -20%;
            opacity: 0;
            position: absolute;
            top: -20%;
            transition: opacity $transition-speed-slow $transition-cubic-bezier;
            width: 140%;
            z-index: -1;
        }
    }
}
