// Buttons
//
// Button styles can be applied to anything by using the .btn class applied. However, typically you'll want to apply these to only `<a>` and `<button>` elements for the best rendering.
// See sass/objects/_buttons.scss.
//
// Styleguide 5


// Button styles and states
//
// Markup:
// <a class="button {$modifiers}" href="#">Link Button</a>
// <button class="button {$modifiers}">Button Element</button>
// <input class="button {$modifiers}" type="submit" value="input[type='submit']">
//
// .button--primary     - Indicate that the button is the primary feature of it's parent, for exmaple a form.
// .button--standout    - A non specific standout button
// .button--success     - A success button, useful for a positive micro-transaction
// .button--warning     - A warning button, to express caution
// .button--danger      - Dangerious things will happen when this is used
// .button--disabled    - Make the button appear as though it's disabled
//
// Styleguide 5.1
$button-border-radius: 60px;

.button,
%button {
    &,
    &:visited {
        @extend %normalis;

        background-color: $color-white;
        border: $border-width-mid solid $color-link;
        border-radius: $button-border-radius;
        color: $color-link;
        cursor: pointer;
        display: inline-block;
        font-family: $font-family;
        font-size: $milli-px;
        font-weight: $font-weight-normal;
        margin: $space $border-width-xlarge $border-width-xlarge;
        padding: $space-half $space-and-half;
        position: relative;
        text-align: center;
        text-decoration: none;
        text-transform: uppercase;
        vertical-align: middle;

        &::before {
            border: $border-width-xxlarge solid $color-link-outline;
            border-radius: $button-border-radius;
            box-sizing: content-box;
            content: '';
            height: 100%;
            left: -$border-width-xxlarge;
            position: absolute;
            top: -$border-width-xxlarge;
            transition: all $transition-speed-slow $transition-cubic-bezier;
            width: 100%;
            z-index: 0;
        }
    }

    &:hover,
    &:active,
    &:focus {
        text-decoration: none;

        &::before {
            border-color: $color-link;
        }
    }

    &:focus {
        box-shadow: 0 0 0 ($border-width-large * 2.5) $color-link-focus;
        outline: 0;
    }

    &[disabled] {
        border-color: $color-disabled;
        color: $color-text-disabled;
        cursor: not-allowed;

        &::before {
            border-color: $color-text-disabled;
        }
    }
}
