.button--warning,
%button--warning {
    &,
    &:visited {
        border-color: darken($color-warning, 10%);
        color: $color-primary;
    }

    &:hover,
    &:active,
    &:focus {
        color: $color-primary;

        &::before {
            border-color: darken($color-warning, 10%);
        }
    }
}
