.button--white,
%button--white {
    background: transparent;

    &,
    &:visited {
        border-color: $color-white;
        color: $color-white;

        &::before {
            border-color: rgba($color-white, .2);
        }
    }

    &:hover,
    &:active,
    &:focus {

        &::before {
            border-color: $color-white;
        }
    }
}
