// Header
//
// This file is intentially sparse and are intended as placeholder for your header styles
// See sass/layout/_header.scss.
//
// Styleguide 18.2

.site-header {
    background-color: $color-white;
    margin-top: 0;
    z-index: 100;

    .container--flush-left {
        @include respond-max($full-width) {
            padding-right: 0;
        }
    }

    .home.one-column & {
        margin-bottom: 0;
    }

    .identity {
        display: none;
    }

    &__links {
        margin-bottom: $space;
        text-align: center;
        width: 100%;

        @include respond-min($small-width) {
            float: right;
            padding-left: $space-half;
            text-align: right;
            width: percentage(2 / 3);
        }

        .list__item {
            margin-bottom: 0;
            margin-right: 0;

            &:not(:first-child) {
                @include respond-min($x-small-width) {
                    margin-left: $space;
                }
            }
        }
    }
}
