.nav--primary-navigation {

    .list {
        align-items: center;
        display: flex;
        justify-content: flex-end;
        margin: 0;
        position: relative;
        width: 100%;
    }

    .list__item {
        padding: $space-half $space-two-thirds;
        position: relative;

        // possibly not great but as it's just perfect council and should never be seen in ie10 just added this as a quick fix
        @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
            /* IE10+ CSS here */
            &:first-child {
                .home & {
                    flex-grow: 1;
                }
            }
        }

        &:nth-child(2) {
            margin-left: auto;
        }

        &:last-child {
            background-color: $color-primary;
            margin-left: $space-two-thirds;
            padding: $space-half;
            transition: background-color $transition-speed $transition-cubic-bezier;

            &:hover {
                background-color: $color-standout;
            }

            .list__link {
                box-shadow: none;
                outline: 0;

                &:focus {
                    .icon-header-search::before {
                        box-shadow: inset 0 0 0 $border-width-large $color-link-focus;
                    }
                }
            }

            .list__link-text {
                @include visuallyhidden;
            }
        }
    }

    .list__link {
        align-items: center;
        // scss-lint:disable ImportantRule
        display: flex !important;
        // scss-lint:enable ImportantRule
        font-size: $milli;
        font-weight: $font-weight-normal;

        &:hover,
        &:focus,
        &:visited {
            color: $color-primary;
            text-decoration: none;

            .list__link-text {
                @include linkUnderlineHover();
            }
        }

        span {
            display: inline-block;
        }

        .icon {
            background-size: percentage(3 / 4);
            margin-right: $space-quarter;
            transition: background $transition-speed $transition-cubic-bezier;

            @include respond-max($small-width) {
                height: $space-and-two-thirds;
                width: $space-and-two-thirds;
            }

            @include respond-max($mid-width) {
                margin-right: 0;
            }
        }

        .icon-header-menu {
            background-size: percentage(3 / 4) + 5%;
        }

        .icon-header-search {
            margin: 0;

            &::before {
                content: '';
                height: 100%;
                left: 0;
                position: absolute;
                top: 0;
                width: 100%;
            }
        }
    }

    .list__link-text {
        @include linkUnderlineStyling($color: $color-primary, $bg-color: $color-white);

        @include respond-max($small-width) {
            @include visuallyhidden;
        }
    }
}
