$arrow-width: $space-double;
$arrow-height: $space-two-thirds;

.boxed--arrow,
%boxed--arrow {
    padding: $space-double $space 0;

    .arrow-target {
        padding-bottom: $space-double;
        position: relative;

        &::before {
            background-color: transparent;
            border-color: $color-standout transparent transparent;
            border-style: solid;
            border-width: $arrow-height $arrow-width / 2 0;
            bottom: -$arrow-height;
            content: '';
            height: 0;
            left: 0;
            position: absolute;
            top: initial;
            width: 0;
        }
    }
}
