$search-input-border-width: 5px;

.search {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
}

.js .search {
    height: 100vh;
    left: 0;
    pointer-events: none;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1000;

    &::before {
        background: rgba($color-senary, .95);
        content: '';
        height: 100%;
        opacity: 0;
        position: absolute;
        right: 0;
        top: 0;
        transition: opacity $transition-speed-slow $transition-cubic-bezier;
        width: 100%;
    }

    &.is-open {
        pointer-events: auto;

        &::before {
            opacity: 1;
        }
    }
    // scss-lint:disable ImportantRule
    &.is-delayed[aria-hidden="true"] {
        display: flex !important;
    }
    // scss-lint:enable ImportantRule
}

.search__close {
    display: none;
    opacity: 0;
    transition: opacity $transition-speed-slow $transition-cubic-bezier;
}

.search.is-open .search__close {
    display: block;
    opacity: 1;
}

.search__form {
    margin: $space 0;
    width: percentage(3 / 4);
}

.search__label {
    @include visuallyhidden;
}

.search__input {
    -webkit-appearance: none;
    background: transparent;
    border: 0;
    border-bottom: $search-input-border-width solid $color-primary;
    border-radius: 0;
    box-sizing: border-box;
    color: $color-primary;
    display: inline-block;
    font-family: $font-family-heading;
    font-size: $giga;
    font-weight: $font-weight-bold;
    outline: 0;
    width: 100%;

    @include respond-max($small-width) {
        font-size: $kilo;
    }

    &:focus {
        box-shadow: 0 0 0 $border-width-large $color-quaternary;
    }

    .js & {
        transform: scale3d(0, 1, 1);
        transform-origin: 0 50%;
        transition: transform $transition-speed $transition-cubic-bezier;
    }

    .js .search.is-open & {
        transform: scale3d(1, 1, 1);
        transition-duration: .5s;
    }
}

.search__input::-webkit-input-placeholder {
    /* WebKit, Blink, Edge */
    color: $color-primary;
    opacity: 1;

    .js & {
        opacity: 0;
        transition: opacity $transition-speed $transition-cubic-bezier;
        transition-delay: .4s;
        visibility: hidden;
    }

    .js .search.is-open & {
        display: block;
        opacity: 1;
        visibility: visible;
    }
}

.search__input::-webkit-search-cancel-button,
.search__input::-webkit-search-decoration {
    -webkit-appearance: none;
}

.search__input::-ms-clear {
    display: none;
}

.button.search__button {
    background: transparent;
    border: 0;
    margin: 0;
    padding: $space;
    position: absolute;
    right: 0;
    top: calc(50% - #{$search-input-border-width / 2});
    transform: translateY(-50%);

    .js & {
        opacity: 0;
        transition: opacity $transition-speed $transition-cubic-bezier;
        transition-delay: .4s;
        visibility: hidden;
    }

    .js .search.is-open & {
        display: block;
        opacity: 1;
        visibility: visible;
    }

    &::before {
        display: none;
    }

    &:focus {
        box-shadow: 0 0 0 $border-width-large $color-quaternary;
        outline: none;
    }

    .icon {

        @include respond-min($small-width) {
            height: $space-triple;
            width: $space-triple;
        };
    }
}

.search__input-container {
    position: relative;
}

.search__info {
    color: $color-primary;
    display: block;
    font-weight: $font-weight-bold;
    margin: 0;
    opacity: 0;
    padding: $space-two-thirds 0;
    text-align: right;
    transform: translateY(50px);
    transition: opacity $transition-speed $transition-cubic-bezier,
                transform $transition-speed $transition-cubic-bezier;
    width: 100%;

    .search.is-open & {
        opacity: 1;
        transform: translateY(0);
    }
}

.search__related {
    display: block;
    text-align: left;
    width: percentage(3 / 4);
}

.search__suggestion {

    @include respond-min($large-width) {
        width: percentage(3 / 4);
    }

    .js & {
        opacity: 0;
        transform: translateY(50px);
        transition: opacity $transition-speed $transition-cubic-bezier,
                    transform $transition-speed $transition-cubic-bezier;
    }

    .js .search.is-open & {
        opacity: 1;
        transform: translateY(0);
    }
}

.search__suggestion-title {
    color: $color-primary;
    font-size: $beta;
    margin: 0 0 $space-half;
}

.search__suggestion-list {
    list-style: none;
    margin: 0;
}

.search__suggestion-list-item {
    display: inline-block;
    padding: 0 $space-two-thirds .5em 0;
}

.search__suggestion-list-link {
    @include linkUnderlineStyling($color: $color-primary, $bg-color: rgba(228, 233, 244, .95));
    color: primary;
    font-size: $epsilon;
    font-weight: $font-weight-normal;

    &:hover,
    &:focus,
    &:visited {
        @include linkUnderlineHover();
        color: $color-primary;
        text-decoration: none;
    }

    &:focus {
        box-shadow: 0 0 0 $border-width-large $color-quaternary;
    }
}

.search__suggestion p {
    margin: $space-two-thirds 0 0;
}
