// Output of the Xinha document editor
.editor {
    // scss-lint:disable IdSelector,SelectorFormat
    .text_align_left {
        text-align: left;
    }

    .text_align_right {
        text-align: right;
    }

    .text_align_center {
        text-align: center;
    }

    .indent {
        margin-left: 20px;
    }

    .indent2 {
        margin-left: 40px;
    }

    strong {
        font-weight: 700;
    }

    em {
        font-style: italic;
    }

    blockquote {
        background-color: $color-off-cream;
        line-height: $beta-px;
        padding: $space-double;

        p {
            @extend .delta;
            font-family: $font-family-heading;
            font-weight: $font-weight-bold;
            margin: 0;
            padding-top: $space-and-half;
            position: relative;

            &::before {
                background-color: $color-primary;
                content: '';
                height: 4px;
                position: absolute;
                top: 0;
                width: $space-double;
            }
        }

        cite {
            display: block;
            font-style: normal;
            font-weight: $font-weight-normal;
            margin-top: $space-half;
        }
    }

    .snippet,
    iframe {
        max-width: 100%;
    }

    .underline_text {
        text-decoration: underline;
    }

    .strikethrough_text {
        text-decoration: line-through;
    }

    .underline_and_strikethrough_text,
    .underline_text.strikethrough_text {
        text-decoration: line-through underline;
    }

    sup,
    .superscript_text {
        vertical-align: super;
    }

    sub,
    .subscript_text {
        vertical-align: sub;
    }

    .float_left {
        float: left;
    }

    .float_right {
        float: right;
    }

    .image_centre {
        display: block;
        margin: 0 auto;
    }

    td.highlightCell,
    tr.highlightRow td {
        background: #cfc;
    }

    tr.zebra {
        background: #ffc;
    }

    abbr {
        border-bottom: 1px dotted #ccc;
        cursor: help;
    }

    .placeholder {
        background: #eee;
        border: 1px solid #ccc;
        color: #333;
        padding: 2em;
    }

    #editable {
        border: $border-width-mid dashed #999;
        padding: 10px;
    }

    #editable::after {
        clear: both;
        content: '.';
        display: block;
        height: 0;
        visibility: hidden;
    }

    #toolbar {
        background-color: #eee;
        border: 1px solid #ddd;
        padding: .5em;
    }

    .imageCaption {
        @extend .image--caption;

        &.float_left {
            margin-left: 0;
            margin-right: $space;

            @include respond-min($small-width) {
                margin-right: $space-and-half;
            }
        }

        &.float_right {
            margin-left: $space;
            margin-right: 0;

            @include respond-min($small-width) {
                margin-left: $space-and-half;
            }
        }

        &.image_centre {
            margin-left: auto;
            margin-right: auto;
        }

        p {
            @extend .image--caption__text;
        }
    }

    ul,
    ol {
        font-size: $zeta;
        list-style: none;
        margin: $space-half 0 $space $space-triple;

        ul,
        ol {
            margin-left: $space-double;
        }
    }

    ul {

        li {
            position: relative;

            &::before {
                background: $color-quaternary;
                border-radius: 4px;
                content: '';
                display: block;
                height: 8px;
                left: -28px;
                position: absolute;
                top: $space-half;
                width: 8px;
            }
        }
    }

    ol {
        counter-reset: step-counter;

        li {
            counter-increment: step-counter;
            position: relative;

            &::before {
                background: $color-secondary;
                border-radius: 13px;
                content: counter(step-counter);
                display: block;
                font-size: .8em;
                height: 26px;
                left: -35px;
                padding-top: $space-eighth;
                position: absolute;
                text-align: center;
                top: 0;
                width: 26px;
            }
        }
    }

    li {
        margin-top: $space-two-thirds;
    }
    // scss-lint:enable IdSelector,SelectorFormat
}
