// ========================================================
//  General Settings
// ========================================================
//  Contains settings for Typography, Spacing, Breakpoints
//  and some helpers for debugging.

// ========================================================
//  Typography
// ============

//  Base Type Size
// ================
$base-font-size:                16px;
$base-line-height:              24px;
$line-height-ratio:             $base-line-height / $base-font-size;

//  Oversized Type Range
// ======================
$giga-px:                       80px;
$giga:                          $giga-px / $base-font-size * 1rem;

$mega-px:                       72px;
$mega:                          $mega-px / $base-font-size * 1rem;

$kilo-px:                       48px;
$kilo:                          $kilo-px / $base-font-size * 1rem;

//  Standard Type Range
// =====================
$alpha-px:                      50px;
$alpha:                         $alpha-px / $base-font-size * 1rem;

$beta-px:                       40px;
$beta:                          $beta-px / $base-font-size * 1rem;

$gamma-px:                      36px;
$gamma:                         $gamma-px / $base-font-size * 1rem;

$delta-px:                      30px;
$delta:                         $delta-px / $base-font-size * 1rem;

$epsilon-px:                    20px;
$epsilon:                       $epsilon-px / $base-font-size * 1rem;

$zeta-px:                       18px;
$zeta:                          $zeta-px / $base-font-size * 1rem;

$normalis-px:                   $base-font-size;
$normalis:                      $normalis-px / $base-font-size * 1rem;

//  Small Type Range
// ==================
$milli-px:                      15px;
$milli:                         $milli-px / $base-font-size * 1rem;

$micro-px:                      14px;
$micro:                         $micro-px / $base-font-size * 1rem;

//  Typefaces
// ===========
$font-family:                   'Asap', sans-serif;
$font-family-heading:           'Noto Serif', serif;

//  Weights
// =========
$font-weight-normal:            400;
$font-weight-bold:              600;
$font-weight-heading:           700;

//  Measure
// =========
$content-max-width:             40em;

// ========================================================




// ========================================================
//  Breakpoints
// =============

//  Variables
// ===========
$xx-large-width:                2600px;
$full-width:                    1280px;
$x-large-width:                 1020px;
$large-width:                   960px;
$mid-width:                     760px;
$small-width:                   600px;
$x-small-width:                 400px;

//  Map
// =====
$breakpoints: (
    'x-small-width'   '(min-width: #{$x-small-width})',
    'small-width'     '(min-width: #{$small-width})',
    'mid-width'       '(min-width: #{$mid-width})',
    'large-width'     '(min-width: #{$large-width})',
    'x-large-width'   '(min-width: #{$x-large-width})',
    'full-width'      '(min-width: #{$full-width})'
);

// ========================================================




// ========================================================
//  Layout
// ========

//  Spacing
// =========
$space:                         $line-height-ratio * 1rem;         // 24px   -- 1.5rem
$space-half:                    $space / 2;                        // 12px   -- .75rem
$space-third:                   $space / 3;                        // 8px    -- .5rem
$space-quarter:                 $space / 4;                        // 6px    -- .375em
$space-fifth:                   $space / 5;                        // 4.8px  -- .3rem
$space-sixth:                   $space-third / 2;                  // 4px    -- .25rem
$space-eighth:                  $space / 8;                        // 3px    -- .1875rem
$space-twelfth:                 $space-sixth / 2;                  // 2px    -- .125rem
$space-two-thirds:              $space-third * 2;                  // 16px   -- 1rem
$space-three-quarters:          $space - $space-quarter;           // 18px   -- 1.125rem
$space-and-eighth:              $space + $space-eighth;            // 27px   -- 1.6875rem
$space-and-sixth:               $space + $space-sixth;             // 28px   -- 1.75rem
$space-and-fifth:               $space + $space-fifth;             // 28.8px -- 1.8rem
$space-and-quarter:             $space + $space-quarter;           // 30px   -- 1.875em
$space-and-third:               $space + $space-third;             // 32px   -- 2rem
$space-and-half:                $space + $space-half;              // 36px   -- 2.25em
$space-and-two-thirds:          $space + $space-two-thirds;        // 40px   -- 2.5em
$space-and-three-quarters:      $space + $space-three-quarters;    // 42px   -- 2.625em
$space-double:                  $space * 2;                        // 48px   -- 3rem
$space-triple:                  $space * 3;                        // 72px   -- 4.5rem
$space-quadruple:               $space * 4;                        // 96px   -- 6rem

//  Gutters
// =========
//  Space between columns
$gutter:                        $space;
$gutter-large:                  $space-and-third;

//  Leading
// =========
//  Space between rows
$slug:                       $gutter;
$slug-small:                 $slug / 2;
$slug-large:                 $gutter-large;

//  Containers
// ============
$container-narrow:              900px;
$container-standard:            $full-width;
$container-wide:                1600px;

//  Widget grid
// =============
//  Used to normalise the widget widths applied in
//  the `Homepage Editor`.
//  Convert
//  |   33%   |   33%   |   33%   |  |
//  into
//  | 33.3333% | 33.3333% | 33.3333% |
$widget-grid: (
    (1, 12),
    (13, 21),
    (22, 29),
    (30, 36),
    (37, 45),
    (46, 54),
    (55, 62),
    (63, 71),
    (72, 78),
    (79, 83),
    (84, 90)
);

// ========================================================




// ========================================================
//  Styling
// =========

//  Borders
// =========
$border-width-small:            1px;
$border-width-mid:              2px;
$border-width-large:            3px;
$border-width-xlarge:           4px;
$border-width-xxlarge:          6px;
$border-width-xxxlarge:         10px;

//  Shadows
// =====================
// $shadow: 0 1px 2px rgba($color-black, .4);

// ========================================================




// ========================================================
//  Interactions
// ==============

//  Transitions
// =============
$transition-speed-fast:         .2s;
$transition-speed:              .3s;
$transition-speed-slow:         .6s;
$transition-cubic-bezier:       cubic-bezier(0, 0, .2, 1);

// ========================================================




// ========================================================
//  Output settings
// =================
$old-ie: false !default;
$includeAccessibilitySettings: false !default;

// ========================================================




// ========================================================
//  Debugging options
// ===================

//  Show baseline grid
// ====================
$show-baseline:                 false;

//  Show media queries
// ====================
$show-mq:                       false;

//  Enable debug mode
// ===================
//  Highlight possible mark-up / accessibility quirks
//  in your code.
//  Debug styles can be found in `helpers/_debug.scss`
$debug-mode:                    false;
