.widget--myaccount-cxm-cases-verbose-list,
.widget--myaccount-cxm-cases-compact {
    .my-cases__list {
        @extend %list;

        border-top: 1px solid $color-primary;
        margin-top: $space-two-thirds;
        padding-top: $space-third;
    }

    .my-cases__item {
        @extend %list__item !optional;

        border-bottom: 1px solid $color-primary;
        padding-bottom: $space-third;
    }

    .my-cases__title {
        margin: 0;
        padding: 0;
    }

    .my-cases__metadata {
        @extend %list;

        margin-top: 0;
    }

    .my-cases__metadata-item {
        display: inline-block;
        font-size: $milli;
    }

    .my-cases__label {
        background: $color-primary;
        color: $color-white;
        font-size: $micro;
        margin-left: $space-quarter;
        padding: $space-eighth;
        text-transform: uppercase;
    }

    .button {
        @extend %button--small;
    }
}
