$carousel-slide-height: 360px;
$carousel-slide-width: 300px;
$carousel-slide-perspective: 350px;
$deco-line-dash: 1270;
$prev-next-height: 100px;
$dots-width: 200px;

.top-tasks-carousel {

    @include respond-min($x-large-width) {
        margin-top: -#{$space-quadruple};
    }

    .multi-carousel {
        margin: 0 auto;
        max-width: $xx-large-width;

        @include respond-min($x-large-width) {
            padding: $space-triple $space-two-thirds $space-quadruple;
        }

        @include respond-min($full-width) {
            padding: $space-triple $space-two-thirds $space-quadruple;
        }
    }

    .carousel__slide {
        display: flex;
        display: inline-block;
        flex-direction: column;
        height: $carousel-slide-height;
        justify-content: flex-end;
        perspective: $carousel-slide-perspective;
        perspective-origin: 50% 50%;
        position: relative;
        width: $carousel-slide-width;

        @include respond-min($x-large-width) {
            margin: 0 -#{$space-two-thirds} 0 0;
        }

        &:hover {
            .carousel__deco--lines path {
                stroke-dashoffset: 0;
            }
        }

        &--yellow {
            .carousel__slide-container {
                background-color: $color-secondary;

                .carousel__deco--lines {
                    stroke: $color-primary;
                }
            }

            .carousel__title-link {
                &:focus {
                    &::before {
                        box-shadow: 0 0 0 $border-width-large $color-quaternary;
                    }
                }
            }

            .list__link {
                &:focus {
                    box-shadow: 0 0 0 $border-width-large $color-quaternary;
                }
            }
        }

        &--blue {
            .carousel__slide-container {
                background-color: $color-quaternary;
            }

            .carousel__title-link,
            .carousel__list .list__item,
            .carousel__list .list__link {
                color: $color-white;
            }

            .carousel__list .list__link {
                @include linkUnderlineStyling($color: $color-white, $bg-color: $color-quaternary);
            }
        }

        &--turquoise {
            .carousel__slide-container {
                background-color: $color-standout;
            }

            .carousel__deco--lines {
                stroke: $color-primary;
            }

            .carousel__list .list__link {
                @include linkUnderlineStyling($color: $color-primary, $bg-color: $color-standout);
            }
        }

        &--blue-dark {
            .carousel__slide-container {
                background-color: $color-primary;
            }

            .carousel__title-link,
            .carousel__list .list__item,
            .carousel__list .list__link {
                color: $color-white;
            }

            .carousel__list .list__link {
                @include linkUnderlineStyling($color: $color-white, $bg-color: $color-primary);
            }
        }

        &--almond {
            .carousel__slide-container {
                background-color: $color-off-cream;
            }

            .carousel__deco--lines {
                stroke: $color-primary;
            }

            .carousel__list .list__link {
                @include linkUnderlineStyling($color: $color-primary, $bg-color: $color-off-cream);
            }

            .carousel__title-link {
                &:focus {
                    &::before {
                        box-shadow: 0 0 0 $border-width-large $color-quaternary;
                    }
                }
            }

            .list__link {
                &:focus {
                    box-shadow: 0 0 0 $border-width-large $color-quaternary;
                }
            }
        }

        &:nth-child(2n) .carousel__slide-container {

            .js & {
                @include respond-min($x-large-width) {
                    animation: 1.6s $transition-cubic-bezier 0s 1 slideDown;
                }
            }
        }

        > * {
            transform: translateZ(0); /* Force correct stacking order */
        }
    }

    @keyframes slideDown {
        0% {
            transform: translateY(0);
        }

        100% {
            transform: translateY($space);
        }
    }

    @keyframes slideUp {
        0% {
            transform: translateY(0);
        }

        100% {
            transform: translateY(-#{$space});
        }
    }

    .carousel__icon {
        align-self: flex-end;
        height: $space-triple;
        width: $space-triple;
    }

    .carousel__slide-container {
        display: block;
        height: 100%;
        margin: 0;
        position: relative;
        transform-origin: 50% 50%;
        transition: .4s .6s $transition-cubic-bezier;
        width: 100%;

        .js & {
            backface-visibility: hidden;

            @include respond-min($x-large-width) {
                animation: 1.6s $transition-cubic-bezier 0s 1 slideUp;
                opacity: 0;
                transform: translateY($space) rotateY(90deg) translateZ(20px);
            }
        }
    }

    .carousel__deco {
        height: 100%;
        left: 0;
        overflow: hidden;
        position: absolute;
        top: 0;
        width: 100%;
    }

    .carousel__deco--lines {
        fill: none;
        stroke: $color-white;
        stroke-width: $border-width-mid;

        path {
            stroke-dasharray: $deco-line-dash;
            stroke-dashoffset: $deco-line-dash;
            transition: stroke-dashoffset $transition-speed-slow;
        }
    }

    .carousel__slide-content {
        display: flex;
        flex-direction: column;
        height: 100%;
        justify-content: flex-start;
        padding: $space-double;
        width: 100%;
    }

    .carousel__title {
        font-weight: $font-weight-normal;
        line-height: 1;
        margin: auto 0 0;
        z-index: 1;
    }

    .carousel__title-link {
        box-shadow: none;
        color: $color-primary;
        text-decoration: none;

        &:focus {
            &::before {
                box-shadow: 0 0 0 $border-width-large $color-link-focus;
            }
        }

        &::before {
            bottom: 0;
            content: '';
            left: 0;
            position: absolute;
            right: 0;
            top: 0;
        }
    }

    .carousel__list {
        margin: $space 0 0;
        z-index: 1;

        .list__item {
            list-style: initial;
            margin-left: $space-three-quarters;
        }

        .list__link {
            @include linkUnderlineStyling($color: $color-primary, $bg-color: $color-secondary);
            background-repeat: no-repeat;
            background-size: 0 100%;
            color: $color-primary;
            font-size: $normalis;
            font-weight: $font-weight-normal;
            text-decoration: none;

            &:hover {
                @include linkUnderlineHover();
            }
        }
    }

    .is-selected {

        .carousel__slide-container {
            opacity: 1;
        }

        &.carousel__slide:nth-child(2n) .carousel__slide-container {
            @include respond-min($x-large-width) {
                transform: translateY($space) rotateY(0deg) translateZ(0);
            }
        }

        &.carousel__slide:nth-child(2n-1) .carousel__slide-container {
            @include respond-min($x-large-width) {
                transform: translateY(-#{$space}) rotateY(0deg) translateZ(0);
            }
        }
    }

    // flickity

    .flickity-viewport {
        overflow: visible;
    }

    .flickity-page-dots {
        bottom: 0;
        display: none;

        @include respond-min($full-width) {
            display: block;
        }

        .dot {
            background-color: $color-quaternary;
            border-radius: 0;
            height: $border-width-xxlarge;
            margin: 0;
            transition: opacity $transition-speed-slow $transition-cubic-bezier;
            width: $dots-width;

            &:hover {
                opacity: 1;
            }
        }
    }

    .flickity-prev-next-button {
        background-color: $color-primary;
        border: $border-width-mid solid $color-white;
        height: $prev-next-height;
        width: $prev-next-height;
        z-index: 2;

        &:focus {
            box-shadow: 0 0 0 ($border-width-large * 2) $color-link-focus;
        }

        svg {
            transition: width $transition-speed $transition-cubic-bezier;
            width: 30%;
        }

        &:hover {
            background-color: $color-primary;
        }

        .arrow {
            fill: $color-white;
        }

        &.previous {
            transform: translate(-60%, -#{$space-and-half});
            transition: transform $transition-speed $transition-cubic-bezier;

            @include respond-max($small-width) {
                transform: translate(-70%, -#{$space-and-half});
            }

            &:hover {
                transform: translate(-55%, -#{$space-and-half});

                @include respond-max($small-width) {
                    transform: translate(-65%, -#{$space-and-half});
                }

                svg {
                    width: 35%;
                }
            }

            svg {
                left: auto;
                right: 10%;
            }
        }

        &.next {
            transform: translate(60%, -#{$space-and-half});
            transition: transform $transition-speed $transition-cubic-bezier;

            @include respond-max($small-width) {
                transform: translate(70%, -#{$space-and-half});
            }

            &:hover {
                transform: translate(55%, -#{$space-and-half});

                @include respond-max($small-width) {
                    transform: translate(65%, -#{$space-and-half});
                }

                svg {
                    width: 35%;
                }
            }

            svg {
                left: 10%;
            }
        }
    }
}
