.list--definition {
    @extend %cf;
}

.list--definition__heading {
    border-top: 1px solid $color-subtle;
    clear: left;
    font-weight: bold;
    padding-top: $space-two-thirds;

    @include respond-min($mid-width) {
        float: left;
        padding: $space-two-thirds $space-half;
        text-align: right;
        width: 25%;
    }
}

.list--definition__content {
    margin: 0;
    padding-bottom: $space-two-thirds;
    padding-top: $space-sixth;

    @include respond-min($mid-width) {
        border-top: 1px solid $color-subtle;
        float: left;
        padding: $space-two-thirds $space-half;
        width: 75%;
    }
}

.list--definition__map,
.list--definition__image {
    display: block;
    margin: $space-sixth 0 $space-fifth;
}

.list--definition__editor {
    @extend %module;

    > *:first-child {
        margin-top: 0;
    }
}
