// Breadcrumb navigation
//
// Renders the list into vertical breadcrumb mode
//
// Markup:
// <ol class="nav nav--breadcrumb">
//   <li><a>Home</a></li>
//   <li><a>Contact</a></li>
//   <li><a>About</a></li>
// </ol>
//
// Styleguide 14.2

.breadcrumb {
    .home-page & {
        @extend %visually-hidden;
    }
}

.breadcrumb__prefix {
    @extend %visually-hidden;
}

.list--breadcrumb {
    display: block;
    margin: 0;
    padding: 0;

    .list__item {
        display: inline;
        font-size: $normalis;
    }

    a {
        @include linkUnderlineStyling($color: $color-primary, $bg-color: $color-subtle);
        color: $color-primary;
        font-weight: $font-weight-normal;
        text-decoration: none;

        &:hover,
        &:focus {
            @include linkUnderlineHover();

            box-shadow: 0 0 0 $border-width-large $color-quaternary;
        }
    }

    span {
        display: inline;
        font-weight: $font-weight-bold;
    }

    .breadcrumb__separator {
        padding: 0 ($space-half - $space-eighth) 0 $space-half;
    }
}
